import React, { useState, useEffect, Fragment } from "react";

import { Button } from "react-bootstrap";
import cuid from "cuid";
import { TimelineDropdown } from "../../../../../partials/display/timeLine/TimelineDropdown";
import TimelineItem from "../../../../../partials/display/timeLine/TimelineItem";
import { getTimelineByContactId } from "../../../crud/contactsCrud";
const queryString = require("query-string");

export default function ContactsTimeline({ contactId }) {
  const [selected, setSelected] = useState([]);

  const [timeline, setTimeline] = useState(null);
  const options = [
    { value: "1", label: "Jimmy" },
    { value: "2", label: "Laura" },
    { value: "3", label: "Tommy" },
    { value: "4", label: "Jane" },
    { value: "5", label: "Mike" },
  ];

  // useEffect(() => {
  //   let query = {};
  //   query.contact = contactId;
  //   if (contactId) {
  //     console.log(contactId);
  //     getTimelineByContactId(new URLSearchParams(query).toString())
  //       .then(({ data: { data } }) => {
  //         setTimeline(data);
  //       })
  //       .catch(() => {});
  //   }
  // }, []);

  useEffect(() => {

    let selectedOptions = selected.map(function(el) {
      return el.value;
    });
    let query = queryString.stringify(
      { activityType: selectedOptions ,contact:contactId  },
      { arrayFormat: "bracket" }
    );
    getTimelineByContactId(query)
      .then(({ data: { data } }) => {
        setTimeline(data);
      })
      .catch(() => {});


  }, [selected]);

  return (
    <React.Fragment>
      <div className="timeline-body">
        <div className="timeline timeline-5">
          <div className="timeline-item align-items-start timeline-start-button">
            <div className="timeline-label"></div>
            <div className="timeline-badge">
              <Button variant="outline-secondary">Now</Button>
            </div>
            <div className="timeline-content"></div>
          </div>
          {timeline
            ? timeline.map((item, i) => (
                <Fragment key={cuid()}>
                  <TimelineItem
                    date={item.timeline_date}
                    data={item.text}
                    type={item.type}
                  />
                  {console.log(item)}
                </Fragment>
              ))
            : ""}
        </div>
        <div className="timeline-dropdown">
          <TimelineDropdown
            options={options}
            value={selected}
            onChange={setSelected}
          />
        </div>
      </div>
    </React.Fragment>
  );
}
