import React, { useState, useEffect, Fragment } from 'react'
import { getAllMail } from '../../../crud/buyersCrud';
import ContactsMailTable from './BuyerMailTable';

const BuyerMail = () => {

    const [loading, setLoading] = useState(true);
    const [data, setData] = useState({});

    useEffect(() => {
      // console.log("called" + state.contactProfileId);
      getAllMail()
      .then(({data}) => {
        setData(data);
        setLoading(false)
        console.log(data);
      })
      .catch(() => {
  
      });
    }, []);
    return (
        <Fragment>
            <ContactsMailTable data={data}/>
        </Fragment>
    )
}

export default BuyerMail
