import React, { Fragment, useEffect } from "react";
import ThemeButton from "../button/ThemeButton";
import { CharacterCircle } from "../component/characterCircle /CharacterCircle";

const ImagePreview = ({
  files,
  images,
  removeImage,
  selectedImage,
  changeTitle,
  uploadType,
}) => {
  const thumbsContainer = {
    display: "flex",
    flexDirection: "row",
    flexWrap: "wrap",
    marginTop: 16,
  };

  const thumb = {
    display: "inline-flex",
    borderRadius: 2,
    border: "1px solid #eaeaea",
    marginBottom: 8,

    width: 350,
    height: 350,
    padding: 4,
    boxSizing: "border-box",
    position: "relative",
  };

  const closeButton = {
    position: "absolute",
    right: "0px",
  };

  const box = {};
  const thumbInner = {
    display: "flex",
    minWidth: 0,
    overflow: "hidden",
  };

  const img = {
    display: "block",
    width: "auto",
    height: "100%",
  };

  images.map((image, index) => {});
  console.log(images);
  const thumbs = images.map((image, index) => (
    <Fragment>
      <div style={box} className="p-2">
        <div style={thumb} key={image.name}>
          <div style={thumbInner}>
            <div className="position-absolute d-flex flex-column w-100 h-100">
              <div className="d-flex justify-content-between p-3">
                <div>
                  <CharacterCircle character={image.id} />
                </div>
                <div>
                  <input
                    type="checkbox"
                    onBlur={() => selectedImage(image.id)}
                    value={image.id}
                    name="selectedImage"
                  />
                </div>
              </div>
              <div className="d-flex m-auto">
                <ThemeButton icon>
                  <i class="fas fa-tags    "></i>
                </ThemeButton>
                <ThemeButton icon>
                  <i class="fas fa-download    "></i>
                </ThemeButton>
                <ThemeButton icon onClick={() => removeImage(image.id)}>
                  <i class="fas fa-download    "></i>
                </ThemeButton>
              </div>
            </div>

            {uploadType === "image" ? (
              <img src={image.preview} style={img} />
            ) : (
              ""
            )}
            {uploadType === "video" ? (
              <video controls>
                <source src={image.preview+"#t=0.5"} />
                Your browser does not support HTML5 video.
              </video>
            ) : (
              ""
            )}
          </div>
        </div>
        <div class="form-group">
          <input
            onChange={(e) => changeTitle(e.target.value, image.id)}
            type="text"
            id={image.id}
            defaultValue={image.title}
            name="titless"
            class="form-control"
          />
        </div>
      </div>
    </Fragment>
  ));

  return (
    <Fragment>
      <aside style={thumbsContainer}>
        <div class="d-flex flex-wrap">{thumbs}</div>
      </aside>
    </Fragment>
  );
};

export default ImagePreview;
