import React, { Fragment } from 'react'
import { Form } from 'react-bootstrap';


const RadioInput = ({input, type, label, required,name,onClick,ref}) => {

    return (
        <Fragment>
            <Form.Check 
            ref={ref}
            onClick={onClick}
                {...input}
                type={type}
                label={label}
                required = {required ? required : ''}
            />
        </Fragment>
    )
};

export default RadioInput