export function mungeEmailAddress(s) {
  var i = s.indexOf("@");
  var startIndex = (i * 0.2) | 0;
  var endIndex = (i * 0.9) | 0;
  return (
    s.slice(0, startIndex) +
    s.slice(startIndex, endIndex).replace(/./g, "*") +
    s.slice(endIndex)
  );
}

export function stringCompare(a, b) {
  if (a.length !== b.length) {
    return false;
  }
  return a.localeCompare(b) === 0;
}

export function currencyFormatter(num, digits) {
  var si = [
    { value: 1, symbol: "" },
    { value: 1e3, symbol: "k" },
    { value: 1e6, symbol: "M" },
    { value: 1e9, symbol: "G" },
    { value: 1e12, symbol: "T" },
    { value: 1e15, symbol: "P" },
    { value: 1e18, symbol: "E" },
  ];
  var rx = /\.0+$|(\.[0-9]*[1-9])0+$/;
  var i;
  for (i = si.length - 1; i > 0; i--) {
    if (num >= si[i].value) {
      break;
    }
  }
  return (num / si[i].value).toFixed(digits).replace(rx, "$1") + si[i].symbol;
}


export function getRndInteger(min, max) {
  return Math.floor(Math.random() * (max - min) ) + min;
}

export function getPercentage(partialValue, totalValue) {
  return Math.round((100 * partialValue) / totalValue);
} 

export function ucFirst(string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
} 
export function lcFirst(string) {
  return string.charAt(0).toLowerCase()() + string.slice(1);
}

export function spaceRemove(string) {
  return string.replace(/\s/g, '');
} 

export function dotString(string, max, after) {
  if (string.length > max){
    return string.substring(0,after)+'...';
} 
else
return string
}

function pushToArray(arr, obj) {
  const index = arr.findIndex((e) => e.id === obj.id);
  const array = arr;

  if (index === -1) {
    array.push(obj);
  } else {
    array[index] = obj;
  }


  return array;
}