import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import * as buyersRedux from "../../../_redux/buyersRedux";
import { Button } from "react-bootstrap";
import cuid from "cuid";
import TimelineItem from "../../../../../partials/display/timeLine/TimelineItem";
import { TimelineDropdown } from "../../../../../partials/display/timeLine/TimelineDropdown";


export default function BuyerTimeline() {
  const [selected, setSelected] = useState([]);
  const options = [
    { value: "1", label: "Jimmy" },
    { value: "2", label: "Laura" },
    { value: "3", label: "Tommy" },
    { value: "4", label: "Jane" },
    { value: "5", label: "Mike" },
  ];

  const timeline = useSelector((state) => state.buyers.timeline);

  const dispatch = useDispatch();
  useEffect(() => {
    console.log(timeline);
    if (!timeline) {
      console.log("hello");
      dispatch(buyersRedux.actions.buyerFetchTabs({ tab: "timeline", id: 4 })); // dispatch action to fetch contact types
    }
  }, []);

  return (
    <React.Fragment>
      <div className="timeline-body">
        <div className="timeline timeline-5">
          <div className="timeline-item align-items-start timeline-start-button">
            <div className="timeline-label"></div>
            <div className="timeline-badge">
              <Button variant="outline-secondary">Now</Button>
            </div>
            <div className="timeline-content"></div>
          </div>
          {timeline
            ? timeline.map((item, i) => (
                <Fragment key={cuid()}>
                  <TimelineItem
                    date={item.date}
                    data={item.data}
                    type={item.type}
                  />
                  {console.log(item)}
                </Fragment>
              ))
            : ""}
        </div>
        <div className="timeline-dropdown">
          <TimelineDropdown
            options={options}
            value={selected}
            onChange={setSelected}
          />
        </div>
      </div>
    </React.Fragment>
  );
}
