import React, { useState } from "react";
import { connect } from "react-redux";
import * as modal from "../../../_modal/_redux/modalRedux";
import { reduxForm, Field } from "redux-form";
import TextInput from "../../../../partials/form/TextInput";
// import validate from "../../partials/form/validate";
import { Form, Modal, Button, Col, Row } from "react-bootstrap";
import * as diary from "../../_redux/diaryRedux";
import {
  combineValidators,
  composeValidators,
  isRequired,
  hasLengthGreaterThan,
} from "revalidate";
import DateInput from "../../../../partials/form/DateInput";
import cuid from "cuid";

const validate = combineValidators({
  title: isRequired({ message: "The task title is required" }),
  category: isRequired({ message: "The category is required" }),
  description: composeValidators(
    isRequired({ message: "Please enter a description" }),
    hasLengthGreaterThan(4)({
      message: "Description needs to be at least 5 characters",
    })
  )(),
  city: isRequired("city"),
  venue: isRequired("venue"),
  checkbox: isRequired("checkbox"),
  startTime: isRequired("date"),
  endTime: isRequired("date"),
});

const TaskModal = (props) => {
  const {
    edit,
    task_id,
    handleSubmit,
    start,
    end,
    pristine,
    reset,
    submitting,
    invalid,
    closeModal,
    tasksAdd,
    tasksUpdate,
  } = props;
  const [startDate, setStartDate] = useState(start);

  const [endDate, setEndDate] = useState(end);
  const onFormSubmit = async (values) => {
    console.log("task form");
    const newTaskObject = {
      id: cuid(),
      title: values.title,
      start: values.startTime,
      end: values.endTime,
    };
    tasksAdd(newTaskObject);
    closeModal();

    // if (this.props.initialValues.id) {
    //   this.props.updateTask(values);
    //   this.props.history.push(`/tasks/${this.props.initialValues.id}`);
    // } else {
    //   const newTask = {
    //     ...values,
    //     id: cuid(),
    //     hostPhotoURL: '/assets/user.png',
    //     hostedBy: 'Bob'
    //   };
    //   this.props.createTask(newTask);
    //   this.props.history.push(`/tasks/${newTask.id}`);
    // }
  };

  return (
    // <Modal closeIcon='close' open={true} onClose={closeModal}>
    //   <Modal.Header>Test Modal</Modal.Header>
    //   <Modal.Content>
    //     <Modal.Description>
    //       <p>Test Modal... nothing to see here</p>
    //     </Modal.Description>
    //   </Modal.Content>
    // </Modal>
    <Modal
      show={true}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
      onHide={closeModal}
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          Modal heading {edit && task_id ? "Edit" : "not edit"}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form onSubmit={handleSubmit(onFormSubmit)} noValidate>
          <Form.Group as={Row}>
            <Form.Label column sm={2}>
              Title
            </Form.Label>
            <Col sm={10}>
              <Field
                name="title"
                type="text"
                component={TextInput}
                label="Title"
              />
            </Col>
          </Form.Group>

          <Form.Group as={Row}>
            <Form.Label column sm={2}>
              Start Time
            </Form.Label>
            <Col sm={10}>
              <Field
                //  onChange={date => setStartDate(date)}
                selected={start}
                changeDate={setStartDate}
                name="startTime"
                component={DateInput}
                dateFormat="MMMM d, yyyy h:mm aa"
                // placeholder="Task Start Time"
                timeIntervals={15}
                showTimeSelect
                timeFormat="HH:mm"
              />
            </Col>
          </Form.Group>

          <Form.Group as={Row}>
            <Form.Label column sm={2}>
              End Time
            </Form.Label>
            <Col sm={10}>
              <Field
                selected={end}
                name="endTime"
                component={DateInput}
                timeIntervals={15}
                // dateFormat="dd LLL yyyy h:mm a"
                dateFormat="MMMM d, yyyy h:mm aa"
                placeholder="Task End Time"
                showTimeSelect
                timeFormat="HH:mm"
              />
            </Col>
          </Form.Group>

          {/* <Field
                            name='date'
                            type='text'
                            component={DateInput}
                            dateFormat='yyyy/LL/dd HH:mm'
                            timeFormat='HH:mm'
                            showTimeSelect
                            placeholder='Task date'
                        /> */}
          <Button type="submit" disabled={invalid || pristine || submitting}>
            Submit
          </Button>
          {/* <button type="submit" disabled={invalid ||pristine || submitting}>Submit</button> */}
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={closeModal}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
};

const actions = {
  closeModal: modal.actions.closeModal,
  tasksAdd: diary.actions.addTask,
  tasksUpdate: diary.actions.deleteTask,
};
const mapState = (state, ownProps) => {
  let task = {};

  if (typeof state.task != "undefined") {
    task = state.task;
  }

  return {
    start: state.modal.modalProps.start
      ? state.modal.modalProps.start
      : ownProps.task.start,
    end: state.modal.modalProps.end
      ? state.modal.modalProps.end
      : ownProps.task.end,
    popssss: state.task,
    taskModalForm: state.form,
    closeModal: state.closeModal,
    initialValues: { ...ownProps.task },
  }; // Foram name must have to be different
};

export default connect(
  mapState,
  actions
)(
  reduxForm({ form: "taskModal", validate, enableReinitialize: true })(
    TaskModal
  )
);
