import React, { Fragment } from "react";
import { OverlayTrigger, Popover } from "react-bootstrap";
import moment from "moment";
import BuyerAppointment from "../../popover/buyerAppointment/BuyerAppointment";

const TimelineItem = ({ date, data, type }) => {
  console.log(data);
  return (
    <Fragment>
      <div className="timeline-item align-items-start">
        <div className="timeline-label">
          {new Date(date).toLocaleDateString()}
        </div>
        <div className={`timeline-badge ${type}-background `}></div>
        <div className="timeline-content d-flex flex-column">
          {data.length > 0 ? (
            <OverlayTrigger
              trigger="click"
              rootClose={true}
              rootCloseEvent="click"
              placement="auto-start"
              overlay={
                <Popover className="buyerAppointmentPophover">
                  <Popover.Content>
                    {" "}
                    <BuyerAppointment data={data} />
                  </Popover.Content>
                </Popover>
              }
            >
              <span className="mr-3">{data[0].listing} test</span>
            </OverlayTrigger>
          ) : (
            <span className="mr-3">{data[0].listing}</span>
          )}
          <span className="text-muted">
            {moment(date).format('hh:mm A')}
          </span>
        </div>
      </div>
    </Fragment>
  );
};

export default TimelineItem;
