import React from 'react'

// import AddUserDialog from './AddUserDialog'
import clsx from 'clsx'
import IconButton from '@material-ui/core/IconButton'
import { lighten, makeStyles } from '@material-ui/core/styles'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Tooltip from '@material-ui/core/Tooltip'

import SettingsIcon from '@material-ui/icons/Settings';
import ThemeButton from '../../../../partials/button/ThemeButton'
import { useDispatch, connect } from 'react-redux'
import * as modalRedux from '../../../_modal/_redux/modalRedux'
import GlobalFilter from '../../../../partials/table/GlobalFilter'

const useToolbarStyles = makeStyles(theme => ({
    
    root: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(1),
    },
    brandButton: {
        color: '#fff',
        backgroundColor: theme.palette.brand.main,
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: theme.palette.secondary.main,
                backgroundColor: lighten(theme.palette.secondary.light, 0.85),
            }
            : {
                color: theme.palette.text.primary,
                backgroundColor: theme.palette.secondary.dark,
            },
    title: {
        flex: '1 1 100%',
    },
}))

const TableToolbar = props => {
console.log(props)
const {listingType} = props;
    const dispatch = useDispatch();

    const handleAdd = () => {
        dispatch(modalRedux.actions.openModal('AddNewListings'));
    };
    const classes = useToolbarStyles()
    const {
        numSelected,
        // addUserHandler,
        // deleteUserHandler,
        preGlobalFilteredRows,
        setGlobalFilter,
        globalFilter,
    } = props
    return (
        <Toolbar
            className={clsx(classes.root, {
                [classes.highlight]: numSelected > 0,
            })}
        >

            <div className="d-flex bd-highlight w-100 justify-content-between">
                <div className="d-flex bd-highlight">

                    <h5 className=" title"> {listingType? listingType :  ''} Listings<IconButton className={classes.margin}>
                        <SettingsIcon />
                    </IconButton> </h5>
                </div>
                <div className="d-flex">
                <div className="p-2">
                        <ThemeButton icon> <i class="fas fa-download"></i></ThemeButton>
                    </div>
                <div className="p-2">
                        <ThemeButton icon> <i class="fas fa-mobile    "></i> </ThemeButton>
                    </div>
                    <div className="p-2">
                        <ThemeButton icon> <i class="fas fa-envelope"></i></ThemeButton>
                    </div>
                    
                    <div className="p-2">
                        <GlobalFilter
                            preGlobalFilteredRows={preGlobalFilteredRows}
                            globalFilter={globalFilter}
                            setGlobalFilter={setGlobalFilter}
                        />
                    </div>
                    <div className="p-2">
                        <ThemeButton onClick={props.handleChange} className="darkGrey-button text-white"> Filter</ThemeButton>
                    </div>
                    <div className="p-2">
                        <ThemeButton onClick={handleAdd} className="green-button text-white"> Add Zone</ThemeButton>
                    </div>
                </div>

            </div>
        </Toolbar>
    )
}

// const selector = formValueSelector("listingDrawerForm"); // <-- same as form name
export default connect(state => ({
    listingType: state.listings?.listingType
  }))(TableToolbar)
