import React, { Fragment } from "react";
import { Form, Row, Col } from "react-bootstrap";
import cuid from "cuid";
import { Label } from "reactstrap";
import { IconButton } from "@material-ui/core";
import { Field } from "redux-form";
import TextInput from "./TextInput";
import DeleteIcon from "@material-ui/icons/Delete";
import { useEffect } from "react";


const NestedChildInfo = ({ fields, meta: { error, submitFailed }, number, label }) => {


    useEffect(() => {
        if(number > 0){
            console.log(fields);
            let i = 0;
            fields.splice(0, fields.length)
            for (i = 0; i < number; i++) {
                fields.push({});
              }
        }
      }, [number]);

    return (
        <Fragment>
        {fields.map((child, index) => (
          <Fragment key={cuid()}>
                  <Form.Row>
          <Col md={6}>
            <Form.Group as={Row} >
            <Form.Label column sm={3}>
              {label} {index + 1} Name
              {index ? (
                <IconButton
                  onClick={() => fields.remove(index)}
                  color="secondary"
                  aria-label="delete"
                >
                  <DeleteIcon />
                </IconButton>
              ) : (
                ""
              )}
            </Form.Label>
              <Col sm={9}>
                <Field
                  name={`${child}.name`}
                  type="text"
                  component={TextInput}
                  placeholder="Child Name"
                />
              </Col>
            </Form.Group>
          </Col>
  
          <Col md={6}>
            <Form.Group as={Row} >
            <Form.Label column sm={3}>
              {label} {index + 1} Date of Birth
            </Form.Label>
              <Col sm={9}>
                <Field
                  name={`${child}.dob`}
                  type="text"
                  component={TextInput}
                  placeholder="Dob"
                />
              </Col>
            </Form.Group>
          </Col>
        </Form.Row>
          </Fragment>
        ))}
      </Fragment>
    )
}

export default NestedChildInfo
