import React, { Fragment, useContext, useState, useCallback } from "react";
import { useAccordionToggle, Accordion, Card, Button } from "react-bootstrap";
import ArrowDownwardIcon from "@material-ui/icons/ArrowDownward";
import ArrowUpwardIcon from "@material-ui/icons/ArrowUpward";
// eslint-disable-next-line no-restricted-imports
import AccordionContext from "react-bootstrap/AccordionContext";
import { IconButton } from "@material-ui/core";
import AccordionContactBody from "./AccordionContactBody";
import {
  combineValidators,
  isRequired,
  composeValidators,
  hasLengthGreaterThan,
} from "revalidate";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";

const validate = combineValidators({
  title: isRequired({ message: "The event title is required" }),
  category: isRequired({ message: "The category is required" }),
  description: composeValidators(
    isRequired({ message: "Please enter a description" }),
    hasLengthGreaterThan(4)({
      message: "Description needs to be at least 5 characters",
    })
  )(),
  city: isRequired("city"),
  venue: isRequired("venue"),
  checkbox: isRequired("checkbox"),
  startTime: isRequired("date"),
  endTime: isRequired("date"),
});

function ContextAwareToggle({
  eventKey,
  callback,
  handleChange,
  handleActionType,
  reset,
}) {
  const currentEventKey = useContext(AccordionContext);

  const decoratedOnClick = useAccordionToggle(
    eventKey,
    () => callback && callback(eventKey)
  );

  const contactDelete = () => {
    // console.log(props)
    handleChange(eventKey);
    console.log("contactDelete " + eventKey);
  };
  const isCurrentEventKey = currentEventKey === eventKey;

  return (
    <Fragment>
      <div className="d-flex">
        <IconButton
          onClick={(e) => {
            decoratedOnClick();
            handleChange(eventKey);
            handleActionType("View");
          }}
          size="small"
        >
          {isCurrentEventKey ? (
            <ArrowDownwardIcon fontSize="inherit" />
          ) : (
            <ArrowUpwardIcon fontSize="inherit" />
          )}
        </IconButton>
        <IconButton onClick={contactDelete} size="small">
          <i className="fas fa-minus-circle"></i>
        </IconButton>
        <IconButton
          onClick={(e) => {
            decoratedOnClick();
            handleChange(eventKey);
            handleActionType("Edit");
            reset();
          }}
          size="small"
        >
          <i className="fas fa-ellipsis-v"></i>
        </IconButton>
      </div>
    </Fragment>
  );
}

const BuyerContacts = (props) => {
  const { handleSubmit, reset, pristine, submitting, invalid } = props;
  const [actionType, setActionType] = useState("View");
  const [contactId, setContactId] = useState(null);

  const handleChange = (id) => {
    console.log(id);
    setContactId(id);
  };

  const handleActionType = (type) => {
    setActionType(type);
  };
  const onFormSubmit = useCallback((values) => {
    console.log(values);
    // setShow(false);
  }, []);

  console.log(contactId + actionType);

  return (
    <Accordion defaultActiveKey="0">
      <form className="contactForm" onSubmit={handleSubmit(onFormSubmit)}>
        <div className="d-flex justify-content-end pb-3">
          <Button
            className="theme-button green-button"
            type="submit"
            disabled={invalid || pristine || submitting}
          >
            Save
          </Button>
        </div>
        <Card className="mb-3">
          <Card.Header className="d-flex justify-content-between pl-3 pr-3">
            <h5 className="title">Adam Marshall</h5>
            <ContextAwareToggle
              handleActionType={handleActionType}
              handleChange={handleChange}
              eventKey="10"
              reset={reset}
            />
          </Card.Header>
          <Accordion.Collapse eventKey="10">
            <Card.Body>
              {contactId === "10" ? (
                <AccordionContactBody actionType={actionType} />
              ) : (
                "Loading.."
              )}
              {contactId} {actionType}
            </Card.Body>
          </Accordion.Collapse>
        </Card>
        <Card>
          <Card.Header className="d-flex justify-content-between pl-3 pr-3">
            <h5 className="title">Samantha Marshall</h5>
            <ContextAwareToggle
              handleChange={handleChange}
              handleActionType={handleActionType}
              eventKey="21"
              reset={reset}
            >
              Click me!
            </ContextAwareToggle>
          </Card.Header>
          <Accordion.Collapse eventKey="21">
            <Card.Body>
              {contactId === "21" ? (
                <AccordionContactBody actionType={actionType} />
              ) : (
                "Loading.."
              )}
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </form>
    </Accordion>
  );
};

const actions = {
  // socialFeedPost: news.actions.socialFeedPostAsync,
};
const mapState = (state) => ({
  // loading: state.async.loading,
  // buttonName: state.async.elementName,
});
export default connect(
  mapState,
  actions
)(reduxForm({ form: "buyerContactsForm", validate })(BuyerContacts));
