import React, { Fragment } from "react";
import TextInput from "./TextInput";
import { Field } from "redux-form";
import { Row, Col } from "react-bootstrap";
import DeleteIcon from "@material-ui/icons/Delete";
import { IconButton, Fab } from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";

const NestedSingeInput = ({ fields, label, placeholder, additionalText, type, meta: { error } }) => {
  const nestedInput = {
    flexWrap: "unset",
    margin: ".5rem",
  };
  return (
    <Fragment>
      {fields.map((inputName, index) => (
        <Fragment key={index}>
          <Row style={nestedInput}>
            <Field
              name={`${inputName}`}
              type={type}
              component={TextInput}
              placeholder={placeholder}
            />
            {index != 0 ? (
              <IconButton
                onClick={() => fields.remove(index)}
                color="secondary"
                aria-label="delete"
              >
                <DeleteIcon />
              </IconButton>
            ) : (
              ""
            )}
          </Row>
        </Fragment>
      ))}

      {error && <li className="error">{error}</li>}
      <Row>
        <Col>
          <p>
            <IconButton
              onClick={() => fields.push()}
              aria-label="delete"
              size="small"
              className="brand-icon-button"
            >
              <i className="fas fa-plus-circle"></i>
            </IconButton>
            {additionalText ? additionalText : 'Add alternatives'}
            
          </p>
        </Col>
      </Row>
    </Fragment>
  );
};

export default NestedSingeInput;
