// CREATE =>  POST: add a new contact to the server
export function createContact(contact) {
  return axios.post(CONTACTS_URL, { contact });
}

// READ
export function getAllContacts(data) {
  // return axios.get(CONTACTS_URL);
  console.log(data)
  return axios.get(CONTACTS_URL+'?limit=50&'+data);
}

export function getContactById(id) {
  return axios.get(CONTACTS_URL+'/'+id);
}

export function getNotesByContactId(prams) {
  console.log(WiseBerryAPIEnv+'note?'+prams)
  return axios.get(WiseBerryAPIEnv+'note?'+prams);
}

export function getTimelineByContactId(prams) {
  return axios.get(WiseBerryAPIEnv+'timeline?'+prams);
}

