import React, { useCallback } from "react";
import { reduxForm, FieldArray, formValueSelector } from "redux-form";
import RenderScrapPaper from "./RenderScrapPaper";
import { connect } from "react-redux";

const ScrapPaper = (props) => {
  const handleRowClick = (evt) => {
    console.log(evt);
  };

  const onFormSubmit = useCallback((values) => {
    console.log(values);
    // setShow(false);
  }, []);
  return (
    <div>
      <FieldArray
        selectedData={props.scrapPaper}
        isEditable={props.isEditable}
        name="scrapPaper"
        placeholder="Enter a suburb here.."
        handleRowClick={handleRowClick}
        component={RenderScrapPaper}
      />
    </div>
  );
};

const selector = formValueSelector("scrapPaperForm"); // <-- same as form name

const mapState = (state, ownProps) => {
  const scrapPaper = selector(state, "scrapPaper");
  return {
    scrapPaper,
    initialValues: { ...ownProps.event },
  }; // Foram name must have to be different
};

export default connect(
  mapState,
  null
)(reduxForm({ form: "scrapPaperForm", enableReinitialize: true })(ScrapPaper));
