import React, { Fragment, useState } from "react";
import { OverlayTrigger, Popover } from "react-bootstrap";
import ThemeButton from "../../../button/ThemeButton";
import Dictaphone from "../../../dictaphone/Dictaphone";
import { now } from "lodash";

const NoteForm = (props) => {

    const {addNote } = props;
  const [selectedTags, setSelectedTags] = useState(null);
  const handleVoiceToSpeech = (text) => {
    // alert('called')

    var voiceToText = document.getElementById("note").value + text;
    document.getElementById("note").value = voiceToText;
    console.log("text");
    console.log(text);
    // setGlobalFilter(text);
  };

  const handleAddTags = () => {
    var values = [
      ...document.querySelectorAll("input[id=textTags]:checked"),
    ].map((e) => e.value);
    setSelectedTags(values);

    console.log(values);
  };

  const handleAddNote= () => {
    let obj = {};
    obj.note = document.getElementById("note").value;
    obj.type = selectedTags;
    obj.date = new Date();
    obj.createdBy = 'Kp';
    obj.isPinned = true;
    addNote(obj);
  };

  const TagPopover = (
    <Popover id="popover-contained">
      <Popover.Content>
        <div class="form-check">
          <input
            class="form-check-input"
            type="checkbox"
            value="Wiseberry Website"
            id="textTags"
          />
          <label class="form-check-label">
            Wiseberry Website
          </label>
        </div>
        <hr />
        <div class="form-check">
          <input
            class="form-check-input"
            type="checkbox"
            value="Property Portals"
            id="textTags"
          />
          <label class="form-check-label" >
            Property Portals
          </label>
        </div>
        <hr />
        <div class="form-check">
          <input
            class="form-check-input"
            type="checkbox"
            value="Heading"
            id="textTags"
          />
          <label class="form-check-label">
            Heading
          </label>
        </div>
        <hr />
        <div class="form-check">
          <input
            class="form-check-input"
            type="checkbox"
            value="Description"
            id="textTags"
          />
          <label class="form-check-label" >
            Description
          </label>
        </div>
      </Popover.Content>
    </Popover>
  );

  return (
    <Fragment>
        <div className="form-group d-flex note-form">
          <OverlayTrigger
            rootClose
            onExit={(e) => handleAddTags(e)}
            trigger="click"
            placement="auto-start"
            overlay={TagPopover}
          >
            <div
              onClick={() => console.log("clicked")}
              className="listing-border-left position-absolute"
            ></div>
          </OverlayTrigger>
          <textarea
            className="form-control form-control-lg form-control-solid"
            id="note"
            rows="3"
            placeholder="Type notes"
          ></textarea>
          <Dictaphone handleVoiceToSpeech={handleVoiceToSpeech} />
          {/* <IconButton className="add-note" aria-label="delete">
           
          </IconButton> */}
          <ThemeButton onClick={handleAddNote} className="add-note" icon>
            <i className="fas fa-plus-circle"></i>
          </ThemeButton>
        </div>
    </Fragment>
  );
};

export default NoteForm;
