/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React , { Component, useEffect, PureComponent }  from "react";
import clsx from "clsx";
import { getContactsData, getListingsData  } from "../curd/globalSearch";
import SearchDropdownContact from "./SearchDropdownContact";
import SearchDropdownListing from "./SearchDropdownListing";

export default class SearchResult extends Component {
  state = {
    contacts: [],
    listings: []
  }
  componentDidMount() {

    
    this.setState({
      // contacts: getContactsData(),
      listings: getListingsData()
    });
  }
   
  render() {
    const { contacts, listings } = this.state;
    const { data } = this.props;
console.log(contacts);
    if (!data) {
      return null;
    }

    if (data.length === 0) {
      return (
        <div className="kt-quick-search__result">
          <div className="kt-quick-search__message kt-hidden">
            No record found
          </div>
        </div>
      );
    }

    return (
      <div className="kt-quick-search__result">
        <SearchDropdownContact searchValue={this.props.searchValue}  data={contacts} />
        <SearchDropdownListing searchValue={this.props.searchValue}  />
        {data.map((item, index) => (
          <React.Fragment key={index}>
            {item.type === 1 && (
              <div className="kt-quick-search__item">
                {/* TODO: icons and avatars don't load from array */}

                <div className={clsx({"kt-quick-search__item-img": item.iconImage, "kt-quick-search__item-icon": item.iconClassName})}>
                  {item.iconClassName ? (
                    <i className={item.iconClassName} />
                  ) : item.iconImage ? (
                    <img src={item.iconImage} alt="" />
                  ) : null}
                </div>

                <div className="kt-quick-search__item-wrapper">
                  <a href="#" className="kt-quick-search__item-title">
                    {item.text}
                  </a>
                  <div className="kt-quick-search__item-desc">
                    {item.description}
                  </div>
                </div>
              </div>
            )}

            {item.type === 0 && (
              <div className="kt-quick-search__category">{item.text}</div>
            )}
          </React.Fragment>
        ))}
      </div>
    );
  }
}
