import { persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage";
import { put, takeLatest, delay ,call} from "redux-saga/effects";
import { getAllListings, getListingById } from "../_crud/listingsCrud";

export const actionTypes = {
    ListingsLoading: "[ListingsLoading]",
    ListingFetched: "[ListingFetched]",
    ListingFetchAsync: "[ListingFetchAsync]",
    ListingsFetched: "[ListingsFetched]",
    ListingCreated: "[ListingCreated]",
    ListingUpdated: "[ListingUpdated]",
    ListingDeleted: "[ListingDeleted]",
    ListingsDeleted: "[ListingsDeleted]",
    ListingsStatusUpdated: "[ListingsStatusUpdated]",
};

const initialListingsState = {
    listingLoading: true,
    actionsLoading: false,
    totalCount: 0,
    entities: null,
    listingForEdit: undefined,
    error: null,
    profile: null,
    listingType: null
};

export const reducer = persistReducer(
    { storage, key: "wol-auth", whitelist: ["listings"] },
    (state = initialListingsState, action) => {
        switch (action.type) {

            // case actionTypes.ListingLoading: {
            //     return {
            //         ...state
            //     };
            // }
            case actionTypes.ListingsFetched: {
                const {data} = action.payload;
                return {
                    ...state,
                    entities: data,
                    listLoading: false,
                    listingType: data[0].listingType
                }
            }
            case actionTypes.ListingFetched: {
                const {data} = action.payload;
                return {
                    ...state,
                    profile: data
                }
            }
            case actionTypes.ListingError: {
                return {
                    ...state,
                    loading: false,
                    error: action.error
                };
            }

            default:
                return state;
        }
    }
);

export const actions = {
    listingsLoading: data => ({ type: actionTypes.ListingsLoading, payload:  data }),
    listingFetchAsync: data => ({ type: actionTypes.ListingFetchAsync, payload:  data }),
    // socialFeedPostAsync: data => ({ type: actionTypes.SocialFeedPostAsync, payload: data }),
    listingFetched: data => ({ type: actionTypes.ListingFetched, payload: { data } }),
    listingsFetched: data => ({ type: actionTypes.ListingsFetched, payload: { data } }),
    listingCreated: data => ({ type: actionTypes.ListingCreated, payload: { data } }),
    listingUpdated: data => ({ type: actionTypes.ListingUpdated, payload: { data } }),
    listingDeleted: data => ({ type: actionTypes.ListingDeleted, payload: { data } }),
    listingsDeleted: data => ({ type: actionTypes.ListingsDeleted, payload: { data } }),
    listingsStatusUpdated: data => ({ type: actionTypes.ListingsStatusUpdated, payload: { data } }),
    listingError: error => ({ type: actionTypes.ListingError, payload: { error } })
};


export function* sagaListings() {

    yield takeLatest(actionTypes.ListingsLoading, function* listingsLoading({payload}) {
        console.log('called ListingsLoading');
        console.log(payload);
        const {data} = yield getAllListings(payload);
        // yield delay(2000);
        yield put(actions.listingsFetched(data));
        
    });


    yield takeLatest(actionTypes.ListingFetchAsync, function* listingFetchAsync({payload}) {
        console.log('called socialFeedPostAsync');
        console.log(payload);
        const listing = yield getListingById(payload);
        yield delay(2000);
        yield put(actions.listingFetched(listing));


    });
    // yield takeLatest(actionTypes.SocialFeedPostAsync, function* socialFeedPostAsync({payload: {data}}) {
    //     console.log('called socialFeedPostAsync');
    //     console.log(data);
    //     yield delay(10000);

    // });
}
