import {
  combineValidators,
  isRequired,
  composeValidators,
  hasLengthGreaterThan,
} from "revalidate";
import TextInput from "../../../../../../partials/form/TextInput.js";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import "./addTomail.scss";
import ThemeHeader from "../../../../../../partials/content/ThemeHeader";
