/* eslint-disable no-script-url,jsx-a11y/anchor-is-valid */
import React, { Fragment } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import objectPath from "object-path";
import { ReactComponent as SortNum1Icon } from "../../../_metronic/layout/assets/layout-svg-icons/SortNum1.svg";
import { QuickActions } from "./components/QuickActions";
import * as builder from "../../ducks/builder";
import CloseIcon from "@material-ui/icons/Close";
import { ButtonGroup, Button } from "@material-ui/core";
import * as tab from "../../../app/modules/_tab/_redux/tabRedux";
import cuid from "cuid";

class SubHeader extends React.Component {

  // Delete page from Sub Header

  handleDeleteTab = (tabToDelete) => {
    console.log(tabToDelete);
    this.props.tabDelete(tabToDelete);
  };

  // Call setting Page

  handleOpenTab = () => {
    this.props.callTabManager({ name: 'Builder' });
  };


  handleSelectTab = (currentTab) => {
    console.log('controlled-tab-example-tabpane-' + currentTab)
    let parent = document.querySelector('.tab-content');
    let children = parent.children; // [<div className="child1">]

    for (var i = 0; i < children.length; i++) {
      console.log(children[i].className);
      if (children[i].className == 'fade tab-pane active show') {
        children[i].className = 'fade tab-pane'
        break;
      }
    }

    this.props.selectTab(currentTab);
    document.getElementById('controlled-tab-example-tabpane-' + currentTab).className = 'fade tab-pane active show';
  };

  componentDidMount() {
    console.log(this.props)
  }



  render() {

    const { subheaderClasses, subheaderContainerClasses } = this.props;

    const createTabs = () => {
      const tabs = this.props.tabs;

      const allTabs = tabs.map((tab) => {
        return (
          <Fragment key={cuid()}>
            <span className="kt-subheader__separator kt-subheader__separator--v" />
            <ButtonGroup
              className="subNavGroup"
              variant="contained"
              color="primary"
              aria-label="split button"
              key={cuid()}
            >
              <Button key={cuid()}
                className="subNav"
                onClick={() => this.handleSelectTab(tab && tab.props && tab.props.navTitle ? tab.props.navTitle : tab.name)}
              >
                {tab && tab.props && tab.props.navTitle ? tab.props.navTitle : tab.name}
              </Button>
              <Button
                className="subNavIcon"
                key={cuid()}
                color="primary"
                size="small"
                onClick={() => this.handleDeleteTab(tab)}
              >
                <CloseIcon />
              </Button>
            </ButtonGroup>
          </Fragment>
        );
      });

      return <Fragment>{allTabs}</Fragment>;

    };


    return (
      <Fragment>
        <div className={`kt-subheader kt-grid__item ${subheaderClasses}`} id="kt_subheader">
          <div className={`kt-container ${subheaderContainerClasses}`}>

            {/* Sub Header Main */}

            <div className="kt-subheader__main ">
            {/* <ButtonGroup
                className="subNavGroup"
                variant="contained"
                color="primary"
                aria-label="split button"
                key={cuid()}
              >
                <Button key={cuid()}
                  className="subNav"
                  onClick={() => this.handleSelectTab('NewsPage')}
                >
                  News Page
              </Button>
              </ButtonGroup> */}


              <div id="content">

                <div className="d-flex flex-row bd-highlight mt-2 mb-2">
                  {/* Dynamic Tab Creation start */}

                  {createTabs()}

                  {/* Dynamic Tab Creation end */}
                </div>
              </div>


            </div>
            {/* Sub Header Main End */}
{/* 
            <div id="container">
              <div id="content">

                <div className="d-flex flex-row bd-highlight mb-3">
                  {createTabs()}
                </div>
              </div>
            </div> */}

            {/* SubHeader Tollbar */}
            {/* <div className="kt-subheader__toolbar">
              <div className="kt-subheader__wrapper">
                <button type="button" className="btn kt-subheader__btn-primary">
                  Actions &nbsp;
                  <SortNum1Icon className="kt-svg-icon kt-svg-icon--sm" />
                </button>

                <a className="btn kt-subheader__btn-primary btn-icon" href=""  >
                  <i className="flaticon2-file" />
                </a>

                <a className="btn kt-subheader__btn-primary btn-icon" href="#">
                  <i className="flaticon-download-1" />
                </a>

                <a className="btn kt-subheader__btn-primary btn-icon" href="#">
                  <i className="flaticon2-fax" />
                </a>

                <a className="btn kt-subheader__btn-primary btn-icon" href="#" onClick={() => this.handleOpenTab()}  >
                  <i className="flaticon2-settings" />
                </a>

                <QuickActions />
              </div>
            </div> */}

            {/* SubHeader Toolbar End */}
          </div>
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = store => ({
  config: store.builder.layoutConfig,
  tabs: store.tab.tab,
  menuConfig: store.builder.menuConfig,
  menuHeaderDisplay: objectPath.get(store.builder.layoutConfig, "header.menu.self.display"),
  layout: objectPath.get(store.builder.layoutConfig, 'subheader.layout'),
  fluid: objectPath.get(store.builder.layoutConfig, 'footer.self.width') === 'fluid',
  clear: objectPath.get(store.builder.layoutConfig, 'subheader.clear'),
  isOpen: false,
  subheaderClasses: builder.selectors.getClasses(store, {
    path: "subheader",
    toString: true
  }),
  subheaderContainerClasses: builder.selectors.getClasses(store, {
    path: "subheader_container",
    toString: true
  }),
});



export default withRouter(connect(mapStateToProps, tab.actions)(SubHeader));
