import React, { Fragment } from "react";
import cuid from "cuid";
import { Row, Col } from "react-bootstrap";
import { getLocations } from "../../../../../GeneralApi/GeneralApi";
import AsyncSelect from "react-select/async";
import Item from "../../../../../partials/display/Item";

const BuyerCustomLocation = (props) => {
  const { input, placeholder, label } = props;

  //   Add or Delete Item rom the Select Area / Dropdown
  const handleChange = (value) => {
    input.onChange(value);
  };

  //   Remove Selected Item
  const handleRemove = (label) => {
    input.onChange(
      input.value.filter(function(el) {
        return el.label !== label;
      })
    );
  };

  const getOptions = (input) => {
    return getLocations(input)
      .then(({ data }) => {
        return data;
      })
      .catch(() => {});
  };
  return (
    <Fragment>
      <Row className="pb-3">
        <Col xl="6">
          <Item xs={props.xs} md={props.md} lg={props.lg}  left={label} right={<div className="multiSelect">
                <AsyncSelect
                  name={input.name}
                  placeholder={placeholder}
                  isMulti
                  cacheOptions
                  defaultOptions
                  loadOptions={getOptions}
                  onChange={handleChange}
                />
              </div>}/>
        </Col>
        <Col xl="6">
          <div className="d-flex flex-wrap selectedItems justify-content-start justify-content-lg-start">
            {input.value?.length > 0 &&
              input.value.map((item) => (
                <div key={cuid()} className="btn-group pr-2 pb-2" role="group">
                  <button
                    type="button"
                    className="btn brand-button btn-sm text-white"
                  >
                    {item.label}
                  </button>
                  <button
                    onClick={() => handleRemove(item.label)}
                    type="button"
                    className="btn brand-button btn-sm text-white"
                  >
                    <i className="fas fa-times"></i>
                  </button>
                </div>
              ))}
          </div>
        </Col>
      </Row>
    </Fragment>
  );
};
BuyerCustomLocation.defaultProps = {
  isMulti: true,
  xs: 5,
  md: 4,
  lg: 4
};

export default BuyerCustomLocation;
