import React, { useEffect, useState, useCallback, Fragment } from "react";
import { connect, useDispatch } from "react-redux";
// import * as modal from '../_redux/modalRedux'
import { makeStyles, Button } from "@material-ui/core";
import { reduxForm } from "redux-form";

import {
  combineValidators,
  isRequired,
  composeValidators,
  hasLengthGreaterThan,
} from "revalidate";
import BuyerContactForm from "./BuyerContactForm";

const validate = combineValidators({
  title: isRequired({ message: "The event title is required" }),
  category: isRequired({ message: "The category is required" }),
  description: composeValidators(
    isRequired({ message: "Please enter a description" }),
    hasLengthGreaterThan(4)({
      message: "Description needs to be at least 5 characters",
    })
  )(),
  city: isRequired("city"),
  venue: isRequired("venue"),
  checkbox: isRequired("checkbox"),
  startTime: isRequired("date"),
  endTime: isRequired("date"),
});

const BuyerFormModal = (props) => {
  const { handleSubmit, pristine, submitting, invalid } = props;

  const onFormSubmit = useCallback((values) => {
    console.log(values);
    // setShow(false);
  }, []);

  return (
    <Fragment>
      <div className="buyerFormModal">
        <form className="contactForm" onSubmit={handleSubmit(onFormSubmit)}>
          <div className="d-flex justify-content-between pb-3">
            <div className="header-left d-flex w-100 justify-content-start m-auto">
              <h5 className="brand-color font-weight-bold">Make nd Offer</h5>
            </div>
            <div className="header-right d-flex w-100 justify-content-end">
              <div className="pr-2 ">
                <Button
                  onClick={props.onHide}
                  variant="contained"
                  className="theme-button lightGrey-button"
                >
                  Cancel
                </Button>
              </div>
              <div>
                <Button
                  variant="contained"
                  className="theme-button green-button"
                  type="submit"
                  disabled={invalid || pristine || submitting}
                >
                  Save
                </Button>
              </div>
            </div>
          </div>
          <BuyerContactForm/>
        </form>
      </div>
    </Fragment>
  );
};


const actions = {
  // socialFeedPost: news.actions.socialFeedPostAsync,
};
const mapState = (state) => ({
  // loading: state.async.loading,
  // buttonName: state.async.elementName,
});
export default connect(
  mapState,
  actions
)(reduxForm({ form: "buyerFormModal",
initialValues: {
  address: [""],
  email: [""],
  workAddress: [""],
  otherPhone: [''],
},validate })(BuyerFormModal));
