import React, { Fragment } from "react";
import Select from "react-select";
import cuid from "cuid";

const MultiSelect = (props) => {
  const {edit,  input, options, placeholder , showBottom ,isMulti, defaultValue} = props;

  //   Remove Selected Item
  const handleRemove = (label) => {
    input.onChange(
      input.value.filter(function(el) {
        return el.label !== label;
      })
    );
  };

  const getValue = (opts, val) => {
    // let result = val?.map(a => a.value);
    return opts.filter(o => val.includes(o.value));
  };

const handleChange =(e) => {
  let result = e?.map(a => a.value);
  input.onChange(result)
}

  return (
    <Fragment>

      {edit? <div className="multiSelect">
        <Select
          // {...input}
          onChange={handleChange}
          classNamePrefix="multiSelect"
          hideSelectedOptions={false}
          // onBlur={handleChange}
          // onBlur={()=>console.log(input.value)}
          isLoading= {options.length > 0 ? false : true}
          options={options}
          isMulti={isMulti}
          value={input.value ? getValue(options, input.value) :  defaultValue ? options[0] : null}
          // value={input.value ? input.value : defaultValue ? options[0] : null}
          closeMenuOnSelect={false}
          placeholder={placeholder}
        />
        {showBottom ? <Fragment>
          <div className="d-flex flex-wrap selectedItems pt-3">
          {input.value?.length > 0 &&
            input.value.map((item) => (
              <div key={cuid()} className="btn-group pr-2" role="group">
                <button
                  type="button"
                  className="btn brand-button btn-sm text-white"
                >
                  {item.label}
                </button>
                <button
                  onClick={() => handleRemove(item.label)}
                  type="button"
                  className="btn brand-button btn-sm text-white"
                >
                  <i className="fas fa-times"></i>
                </button>
              </div>
            ))}
        </div>
        </Fragment> 
          : ''}

            </div> : <h6>{input.value.value}</h6>}
      
    </Fragment>
  );
};

MultiSelect.defaultProps = {
  isMulti: true, //  pass true or false to make that specific Select Field multiple input accept or not. By default it's can accept multiple input.
  edit: true,   // pass true or false to make that specific field editable or not. By default it's editable
  showBottom: false  // pass true or false to make selected options Bottom of the option field.
 };

export default MultiSelect;
