import React, { Fragment } from "react";
import { Col, Form, Row } from "react-bootstrap";
import { Field } from "redux-form";
import MultiSelect from "../MultiSelect";

const FormPassions = ({ labelTop,fieldArray }) => {
  const userOptions = [
    {
      label: "Erika",
      value: "1",
    },
    {
      label: "Julia",
      value: "2",
    },
    {
      label: "Sarah",
      value: "3",
    },
  ];
  return (
    <Fragment>
      <Form.Group as={labelTop? Col : Row } controlId="formHorizontalPassword">
        {labelTop ? (
          <h5 className="title">Passions</h5>
        ) : (
          <Form.Label column sm={4}>
            Passions:
          </Form.Label>
        )}

        <div className={labelTop ? " " : "col-sm-8"} >
          <Field
            name={fieldArray ? `${fieldArray}.passions` : 'passions'}
            placeholder="Select passions"
            component={MultiSelect}
            showBottom={true}
            options={userOptions}
          />
        </div>
      </Form.Group>
    </Fragment>
  );
};

FormPassions.defaultProps = {
  labelTop: true,
};

export default FormPassions;
