const IndeterminateCheckbox = React.forwardRef(
  ({ indeterminate, ...rest }, ref) => {
    const defaultRef = React.useRef()
    const resolvedRef = ref || defaultRef

    React.useEffect(() => {
      resolvedRef.current.indeterminate = indeterminate
    }, [resolvedRef, indeterminate])

    return (
      <>
      {/* e.stopPropagation() will stop it from row onClick */}
        <Checkbox onClick={(e) => e.stopPropagation()} className='table-checkbox' ref={resolvedRef} {...rest} /> 
      </>
    )
  }
)

function Table({ columns, data, update, handleChange }) {
  const classes = useStyles();
  // Use the state and functions returned from useTable to build your UI

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    preGlobalFilteredRows,
    setGlobalFilter,
    prepareRow,
    state: { sortBy, globalFilter, selectedRowIds},
  } = useTable(
    {
      columns,
      data,
    },
    
    useGlobalFilter, // useGlobalFilter!
    useSortBy,
    useRowSelect,
    hooks => {
      hooks.allColumns.push(columns => [
        // Let's make a column for selection
        {
          id: 'selection',
          // The header can use the table's getToggleAllRowsSelectedProps method
          // to render a checkbox.  Pagination is a problem since this will select all
          // rows even though not all rows are on the current page.  The solution should
          // be server side pagination.  For one, the clients should not download all
          // rows in most cases.  The client should only download data for the current page.
          // In that case, getToggleAllRowsSelectedProps works fine.
          Header: ({ getToggleAllRowsSelectedProps }) => (
            <div>
              <IndeterminateCheckbox {...getToggleAllRowsSelectedProps()} />
            </div>
          ),
          // The cell can use the individual row's getToggleRowSelectedProps method
          // to the render a checkbox
          Cell: ({ row }) => (
            <div>
              <IndeterminateCheckbox {...row.getToggleRowSelectedProps()} />
            </div>
          ),
        },
        ...columns,
      ])
    }
  );
  console.log(rows);
  console.log(preGlobalFilteredRows);
  const dispatch = useDispatch();
  React.useEffect(() => {
    console.log("sort");
  }, [sortBy]);
  // Render the UI for your table
  const handleRowClicked = ({ original }) => {
    // dispatch(
    //   modalRedux.actions.openModal("ContactProfileModal", {
    //     contactProfileId: original.id,
    //   })
    // );
    console.log(original);
  };

  const deleteUserHandler = event => {
    console.log(event);
    // const newData = removeByIndexs(
    //   data,
    //   Object.keys(selectedRowIds).map(x => parseInt(x, 10))
    // )
    // setData(newData)
  }
  const popover = (
    <AddToMail/>
  );
  return (
    <Fragment>
      <TableToolbar

        handleChange={handleChange}
        numSelected={selectedRowIds}
        deleteUserHandler={deleteUserHandler}
        preGlobalFilteredRows={preGlobalFilteredRows}
        setGlobalFilter={setGlobalFilter}
        globalFilter={globalFilter}
      />

      <div className="tableFixHead theme-table-striped">
        <table {...getTableProps()} className="table">
          <thead>
            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map((column) => (
                  <th {...column.getHeaderProps()}>
                    {column.render("Header")}
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody {...getTableBodyProps()}>
            {rows.map((row, i) => {
              prepareRow(row);
              return (
                <OverlayTrigger key={i} trigger="click" placement="right" overlay={popover}>
                <tr
                  {...row.getRowProps()}
                >
                  {row.cells.map((cell, i) => {
                    return (
                      <td key={i} {...cell.getCellProps()}>{cell.render("Cell")}</td>
                    );
                  })}
                </tr>
              </OverlayTrigger>
                
              );
            })}
            <tr>
              <td>
                <Waypoint onEnter={update} />
              </td>
            </tr>
            
          </tbody>
        </table>
      </div>
    </Fragment>
  );
}

function BuyerMailTable({ handleChange }) {
  const [items, setItems] = useState(null);

  // setItems(data.data);
console.log(items)
  useEffect(() => {
    console.log('useeffect')
    getAllMail()
      .then(({data}) => {
        setItems(data.data);
        console.log(data.data);
      })
      .catch(() => {});
  }, []);
  const columns = React.useMemo(
    () => [
      {
        Header: "Mail",
        columns: [
          {
            Header: "Date",
            accessor: "date",
            Cell: ({ cell: { value } }) => (
            <p>{moment(value).format("DD/MM/YYYY")}</p>
            ),
          },
          {
            Header: "Time",
            accessor: "id",
            Cell: ({ cell: {
              row: { original },
            }, }) => (
            <p>{moment(original.date).format("hh : m")}</p>
            ),
          },
          {
            Header: "By",
            accessor: "from",
            Cell: ({ cell: { value } }) => (
            <p>{value}</p>
            ),
          },
          {
            Header: "Type",
            accessor: "type",
            Cell: ({ cell: { value } }) => (
            <p>{value}</p>
            ),
          },
          {
            Header: "Subject",
            accessor: "subject",
            Cell: ({ cell: { value } }) => (
              <p>{value}</p>
            ),
          }
        ],
      },
    ],
    []
  );
  console.log(items);
  const fetchMoreData = () => {
    setTimeout(() => {
      setItems(items.concat(items));
    }, 1000);
  };

  const datas = React.useMemo(() => items, [items]);

  return (
    <Fragment>
      <div className="listings-table">
      {items ? <Table
        columns={columns}
        data={datas}
        handleChange={handleChange}
        update={fetchMoreData}
      /> : 'Loading ...'}
      </div>
     
    </Fragment>
  );
}

export default BuyerMailTable;
