import React, { useState, useEffect, Fragment } from 'react'
import ContactsMailTable from './ProspectMailTable';
import { getAllMail } from '../../../curd/prospectsCrud';

const ProspectMail = () => {

    const [loading, setLoading] = useState(true);
    const [data, setData] = useState({});

    useEffect(() => {
      // console.log("called" + state.contactProfileId);
      getAllMail()
      .then(({data}) => {
        setData(data);
        setLoading(false)
        console.log(data);
      })
      .catch(() => {
  
      });
    }, []);
    return (
        <Fragment>
            <ContactsMailTable data={data}/>
        </Fragment>
    )
}

export default ProspectMail
