import React, { Fragment, useState } from "react";
import { Form, Col, Row } from "react-bootstrap";
import { FieldArray, Field } from "redux-form";
import TextInput from "../TextInput";
import NestedChildInfo from "../NestedChildInfo";

const ContactFormFamilyDetails = ({ fieldArray }) => {
  const [childrenNumber, setChildrenNumber] = useState(0);

  return (
    <Fragment>
      <h5 className="title">Family Details</h5>

      <Form.Row>
        <Col md={6}>
          <Form.Group as={Row}>
            <Form.Label column sm={4}>
              Marital Status:
            </Form.Label>
            <Col sm={8}>
              <Field
                className="form-control mb-1 mb-sm-0"
                name={
                  fieldArray ? `${fieldArray}.maritalStatus` : "maritalStatus"
                }
                component="select"
              >
                <option>Select a marital Status</option>
                <option value="ff0000">Home Phone</option>
                <option value="00ff00">Office Phone</option>
                <option value="0000ff">Blue</option>
              </Field>
            </Col>
          </Form.Group>
        </Col>

        <Col md={6}>
          <Form.Group as={Row} controlId="formHorizontalPassword">
            <Form.Label column sm={4}>
              HouseHold Income:
            </Form.Label>
            <Col sm={8}>
              <Field
                className="form-control mb-1 mb-sm-0"
                name={
                  fieldArray
                    ? `${fieldArray}.houseHoldIncome`
                    : "houseHoldIncome"
                }
                component="select"
              >
                <option>Select Household income range</option>
                <option value="ff0000">Home Phone</option>
                <option value="00ff00">Office Phone</option>
                <option value="0000ff">Blue</option>
              </Field>
            </Col>
          </Form.Group>
        </Col>
      </Form.Row>

      <Form.Row>
        <Col md={6}>
          <Form.Group as={Row}>
            <Form.Label column sm={4}>
              Number of Children:
            </Form.Label>
            <Col sm={8}>
              <Field
                name={
                  fieldArray ? `${fieldArray}.childrenNumber` : "childrenNumber"
                }
                type="text"
                component={TextInput}
                placeholder="Number of Children"
                onBlur={(evt) => setChildrenNumber(evt.target.value)}
              />
            </Col>
          </Form.Group>
        </Col>

        <Col md={6}>
          <Form.Group as={Row} controlId="formHorizontalPassword">
            <Form.Label column sm={4}>
              Pet Names:
            </Form.Label>
            <Col sm={8}>
              <Field
                name={fieldArray ? `${fieldArray}.petNames` : "petNames"}
                type="text"
                component={TextInput}
                placeholder="Pet Names"
                value={childrenNumber}
                handleInputChange={setChildrenNumber}
              />
            </Col>
          </Form.Group>
        </Col>
      </Form.Row>
      {/* <input type="text" onBlur={}></input> */}

      {childrenNumber > 0 ? (
        <FieldArray
          number={childrenNumber}
          name={fieldArray ? `${fieldArray}.childrenInfo` : "childrenInfo"}
          label="Child"
          type="text"
          component={NestedChildInfo}
        />
      ) : (
        ""
      )}
    </Fragment>
  );
};
ContactFormFamilyDetails.defaultProps = {
  fieldArray: "",
 };
export default ContactFormFamilyDetails;
