import React from 'react'

const Item = ({left, right, col,md, lg, xl ,className, rightClass, isRow , noMargin}) => {
  const preClass = `col-${col} col-md-${md} col-lg-${lg} col-xl-${xl? xl : lg} ${noMargin ? '' : 'm-auto'}  ${className}`
    return (
        <div className="row pb-2">
          <div className={preClass}>

          <div className="font-weight-bold min-content">{left}{isRow ? "" :":"}</div>
          </div>
          <div className={"col "+rightClass}>
            
        <div>{right}</div>
          </div>
      </div>
    )
}

Item.defaultProps = {
  col: "4",
  md: "4",
  lg: "3",
  className: "",
  noMargin: false,
 };

export default Item
