/* eslint-disable no-restricted-imports */
import React, { Fragment, useEffect, useContext } from "react";
import { Col, Row } from "react-bootstrap";
import AccordionContext from "react-bootstrap/AccordionContext";
import { useAccordionToggle, Accordion, Card } from "react-bootstrap";
import ContactFormPersonalDetails from "../partials/ContactFormPersonalDetails";
import ContactFormContactInfo from "../partials/ContactFormContactInfo";
import ContactFormFamilyDetails from "../partials/ContactFormFamilyDetails";
import ContactConnections from "../../display/ContactConnections";
import FormLinkedContacts from "../partials/FormLinkedContacts";
import ContactFormSocialInfo from "../partials/ContactFormSocialInfo";
import FormPassions from "../partials/FormPassions";
import { IconButton } from "@material-ui/core";
import KeyboardArrowLeftIcon from "@material-ui/icons/KeyboardArrowLeft";
import KeyboardArrowDownIcon from "@material-ui/icons/KeyboardArrowDown";
import { Field } from "redux-form";
import { UserImageInput } from "../UserImageInput";
import SortableList from "../sortableForm/SortableList";
import Handle from "../../component/handle/Handle";



function CustomToggle({ children, eventKey }) {
  const currentEventKey = useContext(AccordionContext);

  const decoratedOnClick = useAccordionToggle(eventKey, () =>
    console.log("totally custom!")
  );

  const contactDelete = () => {
    console.log("contactDelete " + eventKey);
  };
  const isCurrentEventKey = currentEventKey === eventKey;

  return (
    <Fragment>
      <h5 className="title">{children}</h5>
      <div className="d-flex">
        <IconButton
          onClick={(e) => {
            decoratedOnClick();
          }}
          size="small"
        >
          {isCurrentEventKey ? (
            <KeyboardArrowDownIcon fontSize="inherit" />
          ) : (
            <KeyboardArrowLeftIcon fontSize="inherit" />
          )}
        </IconButton>
        <IconButton onClick={contactDelete} size="small">
          <i className="fas fa-minus-circle"></i>
        </IconButton>
        <Handle />
      </div>
    </Fragment>
  );
}

const RenderContact = (props) => {
  const {
    fields,
    meta: { error, submitFailed },
    data,
  } = props;
  console.log(data);
  useEffect(() => {
    data.forEach(function(item) {
      fields.push(item);
    });
  }, []);

  return (
    <Fragment>
      {submitFailed && error && <span>{error}</span>}
      {fields ? <SortableList fields={fields}>
        {fields.map((member, index) => (
          <Accordion key={index}>
            <Card>
              {console.log(fields.get(index))}
              <Card.Header className="d-flex justify-content-between pl-3 pr-3">
                <CustomToggle eventKey="0">
                  {fields.get(index).name
                    ? fields.get(index).name
                    : "New Contact"}
                </CustomToggle>
              </Card.Header>
              <Accordion.Collapse eventKey="0">
                <Card.Body>
                  <ContactFormPersonalDetails fieldArray={member} />
                  <Row>
                    <Col md={6}>
                      <Row>
                        <Col md={4}>
                          {" "}
                          <Field name="avatar" component={UserImageInput} />
                        </Col>
                        <Col md={8}>
                          <ContactFormSocialInfo />
                        </Col>
                      </Row>
                    </Col>
                    <Col md={6}>
                      <FormPassions labelTop={false} />
                    </Col>
                  </Row>
                  <hr />
                  <ContactFormContactInfo />
                  <hr />
                  <ContactFormFamilyDetails />
                  <hr />
                  <Row>
                    <Col md={6}>
                      <ContactConnections />
                    </Col>
                    <Col md={6}>
                      <FormLinkedContacts />
                    </Col>
                  </Row>
                </Card.Body>
              </Accordion.Collapse>
            </Card>
          </Accordion>
        ))}
      </SortableList>
       : ''}
      <Accordion>
        <Card>
          <Card.Header className="d-flex justify-content-between pl-3 pr-3">
            <h5 className="title">Additional Contact</h5>
            <div className="d-flex">
              <IconButton onClick={() => fields.push({})} size="small">
                <i class="fas fa-plus-circle"></i>
              </IconButton>
            </div>
          </Card.Header>
        </Card>
      </Accordion>
    </Fragment>
  );
};

export default RenderContact;
