/* eslint-disable no-restricted-imports */
import React from 'react'

import clsx from 'clsx'
import { lighten, makeStyles } from '@material-ui/core/styles'
import Toolbar from '@material-ui/core/Toolbar'
import { Button } from '@material-ui/core'
import GlobalFilter from '../../../../../partials/table/GlobalFilter'
import ThemeButton from '../../../../../partials/button/ThemeButton'


const useToolbarStyles = makeStyles(theme => ({

    root: {
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(1),
    },
    highlight:
        theme.palette.type === 'light'
            ? {
                color: theme.palette.secondary.main,
                backgroundColor: lighten(theme.palette.secondary.light, 0.85),
            }
            : {
                color: theme.palette.text.primary,
                backgroundColor: theme.palette.secondary.dark,
            },
    title: {
        color: theme.palette.brand.main,
        fontWeight: 'bold',
        flex: '1 1 100%',
    },
}))

const TableToolbar = props => {

    const classes = useToolbarStyles()
    const {
        numSelected,
        addHandler,
        deleteHandler,
        preGlobalFilteredRows,
        setGlobalFilter,
        globalFilter,
    } = props

    console.log(numSelected);
    return (
        <Toolbar
            className={clsx(classes.root, {
                [classes.highlight]: numSelected > 0,
            })}
        >
            <div className="d-flex w-100 justify-content-end">
                <div className="pr-2 bd-highlight"> <GlobalFilter
                    preGlobalFilteredRows={preGlobalFilteredRows}
                    globalFilter={globalFilter}
                    setGlobalFilter={setGlobalFilter}
                /></div>
                <div className="pr-2 ">

                    <ThemeButton onClick={deleteHandler} variant="contained" className="lightGrey-button"> Remove</ThemeButton>
                </div>
                <div className="pr-2">
                    <ThemeButton onClick={addHandler} className="green-button">
                        Add
               </ThemeButton>
                </div>
            </div>
        </Toolbar>
    )
}


export default React.memo(TableToolbar);
