// Method from server should return QueryResultsModel(items: any[], totalsCount: number)
// items => filtered/sorted result
export function findBuyers(queryParams) {
  return axios.post(`${CONTACTS_URL}/find`, { queryParams });
}

// UPDATE => PUT: update the buyer on the server
export function updateBuyer(buyer) {
  return axios.put(`${CONTACTS_URL}/${buyer.id}`, { buyer });
}

// UPDATE Status
export function updateStatusForBuyers(ids, status) {
  return axios.post(`${CONTACTS_URL}/updateStatusForBuyers`, {
    ids,
    status
  });
}

// DELETE => delete the buyer from the server
export function deleteBuyer(buyerId) {
  return axios.delete(`${CONTACTS_URL}/${buyerId}`);
}

// DELETE Buyers by ids
export function deleteBuyers(ids) {
  return axios.post(`${CONTACTS_URL}/deleteBuyers`, { ids });
}

