const selector = formValueSelector("prospectDetails"); // <-- same as form name

const mapState = (state, ownProps) => {
  let details = {};

  const isListed = selector(state, "isListed");
  const isRented = selector(state, "isRented");
  const rentalRelation = selector(state, "rentalRelation");
  const listingRelation = selector(state, "listingRelation");

  if (typeof state.event != "undefined") {
    details = state.event;
  }

  return {
    isListed,
    isRented,
    rentalRelation,
    listingRelation,
    initialValues: { ...ownProps.event },
  }; // Foram name must have to be different
};

export default connect(
  mapState,
  null
)(
  reduxForm({ form: "prospectDetails", validate, enableReinitialize: true })(
    ProspectDetailsEdit
  )
);
