import React, {useCallback, useState, useEffect, Fragment} from 'react'
import {useDropzone} from 'react-dropzone'
import { Chip } from '@material-ui/core';
import { Image } from 'react-bootstrap';

 
const centralDivStyle = {
   textAlign: 'center'
}
const avatar = {
  maxWidth: "200px",
  width: "100%", /* or any custom size */
  maxHeight:"200px",
    height: "200px",
    objectFit: "contain"
}

export const  UserImageInput = (props) => {
    const {
        input: { onChange },
        disabled
    } = props;
    
    const [files, setFiles] = useState([]);
    const {getRootProps, getInputProps} = useDropzone({
      accept: 'image/*',
      multiple: false,
      onDrop: acceptedFiles => {
        setFiles(acceptedFiles.map(file => Object.assign(file, {
          preview: URL.createObjectURL(file)
        })));
        onChange(acceptedFiles);
      }
    });

  
    useEffect(() => () => {
      // Make sure to revoke the data uris to avoid memory leaks
      files.forEach(file => URL.revokeObjectURL(file.preview));
    }, [files]);

    if(disabled){
        return null
    }

  return (
           <Fragment>
           <div {...getRootProps()} style={centralDivStyle}>
               <input {...getInputProps()}/>
               { files?.length ? <Image style={avatar} src={files[0].preview} roundedCircle /> : <Image  style={avatar} src="https://devtalk.blender.org/uploads/default/original/2X/c/cbd0b1a6345a44b58dda0f6a355eb39ce4e8a56a.png" roundedCircle />}
           </div>
           </Fragment>       
  )
}
