import React, { Fragment, useEffect, useState } from "react";
import { Table, Form, Col } from "react-bootstrap";
import { Field } from "redux-form";
import TextInput from "../TextInput";
import TextArea from "../TextArea";
import { IconButton } from "@material-ui/core";
import ThemeButton from "../../button/ThemeButton";
import ThemeHeader from "../../content/ThemeHeader";
import cuid from "cuid";
import SortableTable from "../sortableTable/SortableTable";
import Handle from "../../component/handle/Handle";
import RadioInput from "../RadioInput";


const FormFeatures = (props) => {
  const { fields, title } = props;
  const [featuresValue, setFeaturesValue] = useState("");
  useEffect(() => {
    fields.push({
      id: false,
      features: "bedrooms",
      comments: "",
      preference: "",
    });
    fields.push({ id: false, features: "bathrooms" });
    fields.push({ id: false, features: "parking" });
    fields.push({ id: false, features: "landsize" });
  }, []);

  const handleAddFeature = () => {
    let obj = {};
    obj.features = document.getElementById("features").value;
    obj.comments = document.getElementById("featureComments").value;
    obj.preference = document.getElementById("featurePreference")?.value
      ? document.getElementById("featurePreference").value
      : "";
    obj.landSize = document.getElementById("featureLandsize")?.value
      ? document.getElementById("featureLandsize").value
      : "";
    obj.landType = document.getElementById("featuresLandType")?.value
      ? document.getElementById("featuresLandType").value
      : "";
    fields.push(obj);
  };

  const handleRemove = () => {
    for (let i = fields.length - 1; i >= 0; i--) {
      if (fields.get(i).id) {
        fields.remove(i);
      }
    }
  };

  const handleSelectAllToggle = (e) => {
    const all = fields.getAll();
    const selected = document.getElementById("checkToggle").checked
      ? true
      : false;

    fields.removeAll();
    all.forEach((item, index) => {
      all[index].id = selected;
      fields.push(item);
    });
  };
  return (
    <Fragment>
      <ThemeHeader
        classnames="pb-2"
        left={<h5 className="title">{title}</h5>}
        right={<ThemeButton onClick={handleRemove}>Remove</ThemeButton>}
      />
      <div className="tableTheme theme-table-striped">
        <Table striped bordered hover size="sm">
          <thead>
            <tr>
              <th>
                {" "}
                <input
                  id="checkToggle"
                  type="checkbox"
                  onChange={handleSelectAllToggle}
                />
              </th>
              <th>Feature</th>
              <th>Preference</th>
              <th>Comments</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td></td>
              <td>
                <select
                  className="form-control"
                  id="features"
                  onChange={(e) => setFeaturesValue(e.target.value)}
                  className="custom-select"
                >
                  <option value="bedrooms">Bedrooms</option>
                  <option value="bathrooms">Bathrooms</option>
                  <option value="parking">Parking</option>
                  <option value="landsize">Land Size</option>
                </select>
              </td>
              <td>
                {featuresValue !== "landsize" ? (
                  <input
                    type="text"
                    id="featurePreference"
                    className="  form-control fix-rounded-right"
                    required
                  />
                ) : (
                  <Form.Group className="d-flex">
                    <Col sm={6} className="pr-3 pr-md-0 pb-3 pb-md-3">
                      <input
                        type="text"
                        id="featureLandsize"
                        className="  form-control fix-rounded-right"
                        required
                      />
                    </Col>
                    <Col sm={6} className="pr-3 pr-md-0 pb-3 pb-md-3">
                      <select
                        className="form-control"
                        id="featuresLandType"
                        className="custom-select"
                      >
                        <option value="m2">m2</option>
                        <option value="acer">acer</option>
                      </select>
                    </Col>
                  </Form.Group>
                )}
              </td>
              <td className="d-flex">
                <textarea
                  id="featureComments"
                  className="form-control "
                  rows="1"
                ></textarea>{" "}
                <IconButton
                  onClick={handleAddFeature}
                  aria-label="delete"
                  size="small"
                  className="brand-icon-button"
                >
                  <i className="fas fa-plus-circle"></i>
                </IconButton>
              </td>
            </tr>
            </tbody>
            <SortableTable fields={fields}>
            {fields.map((item, index) => (
                <Fragment key={index}>
                  <td>
                    <Field
                      name={`${item}.id`}
                      component={RadioInput}
                      type="checkbox"
                    />
                  </td>
                  <td>
                    <Field
                      className="form-control"
                      name={`${item}.features`}
                      component="select"

                    >
                      <option></option>
                      <option value="bedrooms">Bedrooms</option>
                      <option value="bathrooms">Bathrooms</option>
                      <option value="parking">Parking</option>
                      <option value="landsize">Land Size</option>
                    </Field>
                  </td>
                  <td>
                    {fields?.get(index)?.features !== "landsize" ? (
                      <Field
                        name={`${item}.preference`}
                        component={TextInput}
                        type="text"
                        placeholder="preference"
                      />
                    ) : (
                      <Fragment>
                        <Form.Group className="d-flex">
                          <Col sm={6} className="pr-3 pr-md-0 pb-3 pb-md-3">
                            <Field
                              name={`${item}.landSize`}
                              type="text"
                              component={TextInput}
                              placeholder=""
                            />
                          </Col>
                          <Col sm={6} className="pr-3 pr-md-0 pb-3 pb-md-3">
                            <Field
                              className="form-control"
                              name={`${item}.landType`}
                              component="select"
                            >
                              <option value="m2">m2</option>
                              <option value="acer">acer</option>
                            </Field>
                          </Col>
                        </Form.Group>
                      </Fragment>
                    )}
                  </td>
                  <td className="d-flex">
                    <Field
                      name={`${item}.comments`}
                      component={TextArea}
                      type="text"
                      rows={1}
                      placeholder="Enter comment here"
                    />
                    <Handle />
                  </td>
                  </Fragment>
            ))}   </SortableTable>
          
        </Table>
      </div>
    </Fragment>
  );
};

FormFeatures.defaultProps = {
  title: "Features",
};
export default FormFeatures;
