import React, { Fragment } from 'react'
import { Form, Col, Row } from 'react-bootstrap'
import NestedSingeInput from '../NestedSingeInput'
import { FieldArray, Field } from 'redux-form'
import TextInput from '../TextInput'
import NestedPhone from '../NestedPhone'

const ContactFormContactInfo = ({fieldArray}) => {
    return (
        <Fragment>
      <h5 className='title'>Contact Information</h5>

      <Form.Row>
        <Col md={6}>
          <Form.Group as={Row} >
            <Form.Label column sm={4}>
              Mobile Number:
            </Form.Label>
            <Col sm={8}>
            <Field
                name={fieldArray ? `${fieldArray}.mobile` : 'mobile'}
                type="text"
                component={TextInput}
                placeholder="Mobile Number"
              />
            </Col>
          </Form.Group>
          <FieldArray   name={fieldArray ? `${fieldArray}.otherPhone` : 'otherPhone'}  type="text" component={NestedPhone} />
        </Col>

        <Col md={6}>
          <Form.Group as={Row} controlId="formHorizontalPassword">
            <Form.Label column sm={4}>
              Email Address:
            </Form.Label>
            <Col sm={8}>
            <FieldArray   name={fieldArray ? `${fieldArray}.email` : 'email'}  label="email" type="email" component={NestedSingeInput} />
            </Col>
          </Form.Group>
        </Col>
      </Form.Row>
      {/* <FieldArray name="address" component={renderAddress} /> */}
    </Fragment>
  
    )
}

export default ContactFormContactInfo
