import React, {
  Fragment,
  useContext,
  useState,
  useCallback,
  useEffect,
} from "react";

import {
  combineValidators,
  isRequired,
  composeValidators,
  hasLengthGreaterThan,
} from "revalidate";
import { connect } from "react-redux";
import { reduxForm, FieldArray } from "redux-form";
import ThemeButton from "../../../../../partials/button/ThemeButton";
import RenderContact from "../../../../../partials/form/sortableArrayFields/RenderContact";

const validate = combineValidators({
  title: isRequired({ message: "The event title is required" }),
  category: isRequired({ message: "The category is required" }),
  description: composeValidators(
    isRequired({ message: "Please enter a description" }),
    hasLengthGreaterThan(4)({
      message: "Description needs to be at least 5 characters",
    })
  )(),
  city: isRequired("city"),
  venue: isRequired("venue"),
  checkbox: isRequired("checkbox"),
  startTime: isRequired("date"),
  endTime: isRequired("date"),
});

const ProspectContacts = (props) => {
  const { handleSubmit, reset, pristine, submitting, invalid } = props;

  const [data, setData] = useState([
    { id: 1, name: "Marshal", data: [{ name: "Marshal" }] },
    { id: 2, name: "Sofia", data: [{ name: "Sofia" }] },
    { id: 3, name: "Mark", data: [{ name: "Mark" }] },
  ]);

  const handleChange = (id) => {
    console.log(id);
    const newArr = [...data]; // copying the old datas array
    let newObj = { ...newArr[id] };
    newObj.isOpen = true;
    newArr[id] = newObj;
    setData(newArr);
  };

  const handleActionType = (type, id) => {
    console.log(id);
    const newArr = [...data]; // copying the old datas array
    let newObj = { ...newArr[id] };
    newObj.actionType = type;
    newObj.isOpen = true;
    newArr[id] = newObj;
    setData(newArr);
    // setActionType(type);
  };
  const onFormSubmit = useCallback((values) => {
    console.log(values);
    // setShow(false);
  }, []);

  useEffect(() => {
    let newArr = data; // copying the old datas array
    newArr.map((obj, index) => {
      obj.isOpen = false;
      obj.actionType = "View";
    });
    setData(newArr);
  }, []);

  console.log(data);
  return (
    <Fragment>
      <form className="contactForm" onSubmit={handleSubmit(onFormSubmit)}>
        <div className="d-flex justify-content-end pb-3">
          <ThemeButton disabled={invalid || pristine || submitting} type="submit"  className="theme-button green-button"  >
            Save
          </ThemeButton>
        </div>
        <FieldArray data={data} name="members" component={RenderContact} />
      </form>
    </Fragment>
  );
};

const actions = {
  // socialFeedPost: news.actions.socialFeedPostAsync,
};
const mapState = (state) => ({
  // loading: state.async.loading,
  // buttonName: state.async.elementName,
});
export default connect(
  mapState,
  actions
)(reduxForm({ form: "ProspectContactsForm",initialValues: {
  member: {},
}, validate })(ProspectContacts));
