import React, { useState, useEffect, Fragment } from "react";
import { getAllMail } from "../../../crud/tenantsCrud";
import ThemeHeader from "../../../../../partials/content/ThemeHeader";
import ThemeButton from "../../../../../partials/button/ThemeButton";
import DynamicTableEdit from "../../../../../partials/table/DynamicTableEdit";
import moment from "moment";



function TenantMail() {
  const [items, setItems] = useState(null);
  const [selectedItem, setSelectedItem] = useState(null);
  // setItems(data.data);
console.log(items)
  useEffect(() => {
    console.log('useeffect')
    getAllMail()
      .then(({data}) => {
        setItems(data.data);
        console.log(data.data);
      })
      .catch(() => {});
  }, []);
  const columns = React.useMemo(
    () => [
      {
        Header: "Mail",
        columns: [
          {
            Header: "Date",
            accessor: "date",
            Cell: ({ cell: { value } }) => (
            <p>{moment(value).format("DD/MM/YYYY")}</p>
            ),
          },
          {
            Header: "Time",
            accessor: "id",
            Cell: ({ cell: {
              row: { original },
            }, }) => (
            <p>{moment(original.date).format("hh : m")}</p>
            ),
          },
          {
            Header: "By",
            accessor: "from",
            Cell: ({ cell: { value } }) => (
            <p>{value}</p>
            ),
          },
          {
            Header: "Type",
            accessor: "type",
            Cell: ({ cell: { value } }) => (
            <p>{value}</p>
            ),
          },
          {
            Header: "Subject",
            accessor: "subject",
            Cell: ({ cell: { value } }) => (
              <p>{value}</p>
            ),
          }
        ],
      },
    ],
    []
  );
  console.log(items);
  const fetchMoreData = () => {
    setTimeout(() => {
      setItems(items.concat(items));
    }, 1000);
  };

  const datas = React.useMemo(() => items, [items]);

  return (
    <Fragment>
      <ThemeHeader
        right={
          <Fragment>
            <ThemeButton className='mr-2' icon>
              <i class="fas fa-download    "></i>
            </ThemeButton>
            <div className="pr-2 ">
              <ThemeButton variant="contained" className="lightGrey-button">
                {" "}
                Remove
              </ThemeButton>
            </div>
            <div className="pr-2">
              <ThemeButton className="green-button">Add</ThemeButton>
            </div>
          </Fragment>
        }
      />
      <div className="listings-table">
      {items ? <DynamicTableEdit
            handleChange={(e) => setSelectedItem(e)}
            isEditable={true}
            columns={columns}
            data={datas}
            update={fetchMoreData}
          /> : 'Loading ...'}
      </div>
     
    </Fragment>
  );
}

export default TenantMail;
