import React, { useState, Fragment, useEffect } from "react";
import { Col, OverlayTrigger, Popover, Row } from "react-bootstrap";
import { reduxForm, Field } from "redux-form";
import { connect } from "react-redux";
import {
  combineValidators,
  isRequired,
  composeValidators,
  hasLengthGreaterThan,
} from "revalidate";
import ThemeHeader from "../../../../partials/content/ThemeHeader";
import ThemeButton from "../../../../partials/button/ThemeButton";
import TextInput from "../../../../partials/form/TextInput";
import UserPopOver from "../../../../partials/popover/UserPopOver";
import DynamicTableEdit from "../../../../partials/table/DynamicTableEdit";
import Skeleton from "@material-ui/lab/Skeleton";
import { getAllContacts } from "../../crud/contactsCrud";
const queryString = require("query-string");

const validate = combineValidators({
  title: isRequired({ message: "The event title is required" }),
  category: isRequired({ message: "The category is required" }),
  description: composeValidators(
    isRequired({ message: "Please enter a description" }),
    hasLengthGreaterThan(4)({
      message: "Description needs to be at least 5 characters",
    })
  )(),
  city: isRequired("city"),
  venue: isRequired("venue"),
  checkbox: isRequired("checkbox"),
  startTime: isRequired("date"),
  endTime: isRequired("date"),
});

const AddLinkedContacts = (props) => {
  const { handleSubmit, pristine, closeModal, submitting,onSelect, invalid } = props;
  const [items, setItems] = useState(null);
  const [formData, setFormData] = useState(null);
  const [selectedData, setSelectedData] = useState(null);

  // setItems(data.data);
  console.log(items);
  useEffect(() => {
    console.log("useeffect");
    update();
  }, []);


  const update = (prams) => {
    getAllContacts(prams)
      .then(({ data }) => {
        setItems(data.data);
        console.log(data.data);
      })
      .catch(() => {});
  }

  useEffect(() => {
    if(formData)
    {
      console.log(formData)
      let query = queryString.stringify(formData);
      update(query);
    }

  }, [formData]);

  const handleSave =() => {
    onSelect(selectedData);
    closeModal();
  }

  const columns = React.useMemo(
    () => [
      {
        Header: "Name",
        columns: [
          {
            Header: "Name",
            accessor: "name",
            Cell: ({
              cell: { value },
              cell: {
                row: { original },
              },
              cell: data,
            }) => (
              <Fragment>
                <OverlayTrigger
                  trigger="hover"
                  placement="right"
                  overlay={
                    <Popover id="popover-basic">
                      <UserPopOver data={original} />
                    </Popover>
                  }
                >
                  <p className="max-content ">{value}</p>
                </OverlayTrigger>
              </Fragment>
            ),
          },

          {
            Header: "Email",
            accessor: "emails",
            Cell: ({
              cell: { value },
              cell: {
                row: { original },
              },
              cell: data,
            }) => <p>{value ? value[0]?.value : ""}</p>,
          },
          {
            Header: "Phone",
            accessor: "phones",
            Cell: ({
              cell: { value },
              cell: {
                row: { original },
              },
              cell: data,
            }) => <p>{value ? value[0]?.value : ""}</p>,
          },
        ],
      },
    ],
    []
  );

  return (
    <Fragment>
      <div className="buyerFormModal">
        <Fragment>
          <ThemeHeader
            left={
              <Fragment>
                <h5 className="title">Add linked Contacts</h5>
              </Fragment>
            }
            right={
              <Fragment>
                <Fragment>
                  <div className="pl-2">
                    <ThemeButton onClick={closeModal}>Cancel</ThemeButton>
                  </div>
                  <div className="pl-2">
                    <ThemeButton
                      disabled={selectedData ? false : true}
                      onClick={handleSave}
                      className="green-button"
                    >
                      Save
                    </ThemeButton>
                  </div>
                </Fragment>
              </Fragment>
            }
          />

          <form
            onChange={() =>
              setTimeout(handleSubmit((params) => setFormData(params)))
            }
          >
            <Row>
              <Col md={3}>
                <Field name="name" placeholder="Name" component={TextInput} />
              </Col>
              <Col md={3}>
                <Field
                  name="phone"
                  placeholder="Phone Number"
                  component={TextInput}
                />
              </Col>
              <Col md={3}>
                <Field
                  name="email"
                  placeholder="Email Address"
                  component={TextInput}
                />
              </Col>
              <Col md={3}>
                <Field
                  name="company_name"
                  placeholder="Company Name"
                  component={TextInput}
                />
              </Col>
            </Row>
          </form>

          <div className="dynamic-table pt-2">
            {items ? (
              <DynamicTableEdit
                handleChange={(e) => setSelectedData(e)}
                isEditable={true}
                columns={columns}
                data={items}
                // update={fetchMoreData}
                // handleRowClick={rowClick}
              />
            ) : (
              <div>
                <Skeleton animation="wave" variant="text" />
                <Skeleton animation="wave" variant="text" />
                <Skeleton animation="wave" variant="rect" width="100%" />
              </div>
            )}
          </div>
        </Fragment>
      </div>
    </Fragment>
  );
};

const actions = {
  // socialFeedPost: news.actions.socialFeedPostAsync,
};
const mapState = (state) => ({
  // loading: state.async.loading,
  // buttonName: state.async.elementName,
});
export default connect(
  mapState,
  actions
)(reduxForm({ form: "add-linked-contacts", validate })(AddLinkedContacts));
