import React, { Fragment } from "react";
import Item from "./Item";
import moment from "moment";

const BasicInfo = ({ profile }) => {
  return (
    <Fragment>
      <Item
        left="Email"
        right={
          <Fragment>
            {profile?.emails?.map(function(item, index) {
              return (
                <Fragment>
                  <div key={index}>{item.value}</div>
                </Fragment>
              );
            })}
          </Fragment>
        }
      />

      {profile?.phones?.map(function(item, index) {
        return (
          <Fragment>
            <Item key={index} left={item.name} right={item.value} />
          </Fragment>
        );
      })}

      {profile?.addresses?.map(function(item, index) {
        return (
          <Fragment>
            <Item key={index} left={item?.address_type[0].name} right={item.full_address} />
          </Fragment>
        );
      })}

      <Item
        left="D.O.B"
        right={
          <Fragment>
            {moment(profile.dob).format("DD/MM/YYYY")} (
            {moment().diff(profile.dob, "years")} years)
          </Fragment>
        }
      />
    </Fragment>
  );
};

export default BasicInfo;
