// READ
export function getTopLeaders() {
  return axios.get(WiseBerryAPI + 'top-leaders');
}

export function postPost(prams) {
  return axios.post(WiseBerryAPI + 'post',prams, {
    headers: {
      'Content-Type': 'multipart/form-data'
    }
});
}

export function getAllPosts(prams) {
  return axios.get(WiseBerryAPI + 'post?'+prams);
}

export function getPostLike(id) {
  return axios.get(WiseBerryAPI + 'post/like?id='+id);
}

export function getPostApproved(id) {
  return axios.get(WiseBerryAPI + 'post/approve?id='+id);
}

export function getPostDelete(id) {
  return axios.delete(WiseBerryAPI + 'post/'+id);
}
export function getTopOfficesByListings() {
  return axios.get(WiseBerryAPI + 'top-offices-by-listings' + '?from=2020-01-01&to=2020-05-01');
}

export function getTopOfficesBySales() {
  return axios.get(WiseBerryAPI + 'top-offices-by-sales' + '?from=2020-01-01&to=2020-05-01');
}

export function getTopPointsGame() {
  return axios.get(WiseBerryAPI + 'top-points-game' + '?from=2019-01-01&to=2020-05-01&organisation=4');
}

export function getTopPerformers() {
  return axios.get(WiseBerryAPI + 'top-performers' + '?from=2019-01-01&to=2020-05-01');
}

export function getProspectingRatios() {
  return axios.get(WiseBerryAPI + 'top-prospecting-ratios' + '?from=2019-01-01&to=2020-05-01');
}

export function getListingRatios() {
  return axios.get(WiseBerryAPI + 'top-listing-ratios' + '?from=2019-01-01&to=2020-05-01');
}

export function getBuyerRatios() {
  return axios.get(WiseBerryAPI + 'top-buyer-ratios' + '?from=2019-01-01&to=2020-05-01');
}

