import React, { useState, Fragment, useCallback } from "react";
import { Button, Modal, Form, Col, Row } from "react-bootstrap";
import { reduxForm, Field } from "redux-form";
import { connect } from "react-redux";
import {
  combineValidators,
  isRequired,
  composeValidators,
  hasLengthGreaterThan,
} from "revalidate";
import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css";
import TextInput from "../../../../../../partials/form/TextInput";
import ThemeButton from "../../../../../../partials/button/ThemeButton";
import ThemeHeader from "../../../../../../partials/content/ThemeHeader";
import Item from "../../../../../../partials/display/Item";
import DateInput from "../../../../../../partials/form/DateInput";

const validate = combineValidators({
  title: isRequired({ message: "The event title is required" }),
  category: isRequired({ message: "The category is required" }),
  description: composeValidators(
    isRequired({ message: "Please enter a description" }),
    hasLengthGreaterThan(4)({
      message: "Description needs to be at least 5 characters",
    })
  )(),
  city: isRequired("city"),
  venue: isRequired("venue"),
  checkbox: isRequired("checkbox"),
  startTime: isRequired("date"),
  endTime: isRequired("date"),
});

const SignSoldModal = (props) => {
  const [show, setShow] = useState(true);
  const { handleSubmit, reset, pristine, submitting, invalid, onHide } = props;
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  const onFormSubmit = useCallback((values) => {
    console.log(values);
    // setShow(false);
  }, []);
  return (
    <Fragment>
      <form className="contactForm" onSubmit={handleSubmit(onFormSubmit)}>
        <ThemeHeader
          left={<h5 className="title">Mark as Sold</h5>}
          right={
            <Fragment>
              {" "}
              <div className="pl-2">
                <ThemeButton
                  onClick={onHide}
                  className="lightGrey-button"
                >
                  Cancel
                </ThemeButton>
              </div>
              <div className="pl-2">
                <ThemeButton
                  disabled={invalid || pristine || submitting}
                  type="submit"
                  classNames="green-button"
                >
                  Save
                </ThemeButton>
              </div>
            </Fragment>
          }
        />

        <Row>
          <Col md={6}>
            <Item
              left="Sale Date"
              right={
                <Field
                  name="date"
                  type="text"
                  component={DateInput}
                  placeholder="Sale Date"
                />
              }
            />
          </Col>
          <Col md={6}>
            <Item
              left="Sale Price"
              right={
                <Field
                  name="price"
                  type="text"
                  component={TextInput}
                  placeholder="Sale Price"
                />
              }
            />
          </Col>
        </Row>
     </form>
    </Fragment>
  );
};

const actions = {
  // socialFeedPost: news.actions.socialFeedPostAsync,
};
const mapState = (state) => ({
  // loading: state.async.loading,
  // buttonName: state.async.elementName,
});
export default connect(
  mapState,
  actions
)(reduxForm({ form: "signSoldModal", validate })(SignSoldModal));
