import React, { useEffect, useState, Fragment } from "react";
import { connect } from "react-redux";
import * as modals from "../../../_modal/_redux/modalRedux";
import DefaultModal from "../../../../partials/modal/DefaultModal";
import EditZoneAnalysis from "./editZoneAnalysis/EditZoneAnalysis";
import { getZoneAnalysisEdit } from "../../curd/prospectsCrud";

const ZoneAnalysis = (props) => {
  const { contactProfileId, selectedTab, closeModal } = props;
  const [items, setItems] = React.useState(null);
  console.log(props);
  useEffect(() => {
    // console.log("called" + state.contactProfileId);
    getZoneAnalysisEdit(props.data)
      .then(({data}) => {
        console.log(data);
        setItems(data)
      })
      .catch((e) => {
          console.log(e)
      });
  }, []);

  return (
    <DefaultModal
      show={true}
      size="xl"
      aria-labelledby="contained-modal-title-vcenter "
      centered
      onHide={props.closeModal}
    >

        <EditZoneAnalysis data={items}/>


    </DefaultModal>
  );
};

const actions = {
  openModal: modals.actions.openModal,
  closeModal: modals.actions.closeModal,
};

export default connect(null, actions)(ZoneAnalysis);
