import React, { Component, Fragment } from "react";
import { Formik } from "formik";
import { connect } from "react-redux";
import { TextField } from "@material-ui/core";
import { Link, Redirect } from "react-router-dom";
import { FormattedMessage, injectIntl } from "react-intl";
import * as auth from "./_redux/auth.duck";
import { requestPassword } from "../../modules/auth/curd/auth.crud";
import { faHome } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { mungeEmailAddress } from "../../_helpers/_utils/utils";

class ForgotPassword extends Component {
  state = {
    isRequested: false,
    email: "",
  };
 
  render() {
    const { intl } = this.props;
    const { isRequested, email } = this.state;

    return (
      <div className="kt-grid__item kt-grid__item--fluid  kt-grid__item--order-tablet-and-mobile-1  kt-login__wrapper">
        <div className="kt-login__body">
          <div className="kt-login__form">
            {!isRequested ? (
              <Fragment>
                <div className="kt-login__title">
                  <h3>
                    {/* <FormattedMessage id="AUTH.FORGOT.TITLE" /> */} Forgot
                    Your Password?
                  </h3>
                  <p>
                    Enter your email address or username below <br /> and we'll
                    get you back on trac k
                  </p>
                </div>
                <Formik
                  initialValues={{ email: "" }}
                  validate={(values) => {
                    const errors = {};

                    if (!values.email) {
                      errors.email = intl.formatMessage({
                        id: "AUTH.VALIDATION.REQUIRED_FIELD",
                      });
                    } else if (
                      !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(
                        values.email
                      )
                    ) {
                      errors.email = intl.formatMessage({
                        id: "AUTH.VALIDATION.INVALID_FIELD",
                      });
                    }

                    return errors;
                  }}
                  onSubmit={(values, { setStatus, setSubmitting }) => {
                    requestPassword(values.email)
                      .then((res) => {
                        console.log(res);
                        this.setState({
                          isRequested: true,
                          email: res.data.email,
                        });
                      })
                      .catch(() => {
                        setSubmitting(false);
                        setStatus(
                          intl.formatMessage(
                            { id: "AUTH.VALIDATION.NOT_FOUND" },
                            { name: values.email }
                          )
                        );
                      });
                  }}
                >
                  {({
                    values,
                    status,
                    errors,
                    touched,
                    handleChange,
                    handleBlur,
                    handleSubmit,
                    isSubmitting,
                  }) => (
                    <form onSubmit={handleSubmit} className="kt-form">
                      {status && (
                        <div role="alert" className="alert alert-danger">
                          <div className="alert-text">{status}</div>
                        </div>
                      )}

                      <div className="form-group">
                        <TextField
                          type="email"
                          label="Email or Username"
                          margin="normal"
                          fullWidth={true}
                          name="email"
                          onBlur={handleBlur}
                          onChange={handleChange}
                          value={values.email}
                          helperText={touched.email && errors.email}
                          error={Boolean(touched.email && errors.email)}
                        />
                      </div>

                      <div className="kt-login__actions">
                        <Link to="/auth">
                          <button
                            type="button"
                            className="btn btn-secondary btn-elevate kt-login__btn-secondary"
                          >
                            Back
                          </button>
                        </Link>

                        <button
                          type="submit"
                          className="btn btn-primary btn-elevate kt-login__btn-primary"
                          disabled={isSubmitting}
                        >
                          Submit
                        </button>
                      </div>
                    </form>
                  )}
                </Formik>
              </Fragment>
            ) : (
              <div className="kt-login__title">
                <FontAwesomeIcon icon={faCheckCircle} size="6x"  className='kt-font-brand'/>
                <h3>
                  {/* <FormattedMessage id="AUTH.FORGOT.TITLE" /> */} Password
                  Reset Email Sent
                </h3>
                <p>
                  Thank you. An email has been sent to{" "}
                  {mungeEmailAddress(email)} . Please follow the instruction in
                  the email to reset your password.
                </p>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default injectIntl(connect(null, auth.actions)(ForgotPassword));
