import React, { useEffect, useState } from 'react'
import clsx from "clsx";
import { Badge } from '@material-ui/core';
import RecentActorsIcon from '@material-ui/icons/RecentActors';
import { makeStyles } from '@material-ui/styles';
import { getListingsData  } from "../curd/globalSearch";
import { Spinner } from 'react-bootstrap';
import { useDispatch } from 'react-redux';
import * as tabRedux from '../../_tab/_redux/tabRedux'
const useStyles = makeStyles(theme => ({
    margin: {
      margin: theme.spacing(2),
      marginRight: theme.spacing(3),
    },
  }));


  const SearchDropdownListing = (props) => {
    const [data,setData] = useState({})
    const dispatch = useDispatch();
    useEffect(() => {
      setTimeout(() => {
        setData(getListingsData(props.searchValue));
      }, 1000);
    }, []); // here

    const onClickListing = (e) => {
      e.preventDefault();
      // console.log(e.target.attributes.getNamedItem("profileId").value);
      //  dispatch(modalRedux.actions.openModal('ContactProfileModal',{ contactProfileId: e.target.attributes.getNamedItem("contactProfileId").value }))
      dispatch(tabRedux.actions.callTabManager({name: 'SingleListing',id: e.target.attributes.getNamedItem("listing-id").value, navTitle: 'Listing '+ e.target.attributes.getNamedItem("listing-title").value}));
      console.log('The link was clicked.');
    }
  console.log(data);
    return (
      <div>
          {data && data.length ?
          <div>
        <div className="kt-quick-search__category"> Listings <Badge  color="secondary" badgeContent={data.length}>
            <RecentActorsIcon />
               </Badge></div>
            {data.map((item, index) => (
          <React.Fragment key={index}>
           
              <div className="kt-quick-search__item">
                {/* TODO: icons and avatars don't load from array */}

                <div className='kt-quick-search__item-img'>
                    <img src={item.avatar} alt="" />
                </div>

                <div className="kt-quick-search__item-wrapper">
                  <a href="#" className="kt-quick-search__item-title" listing-id={item.id} listing-title={item.title}  onClick={onClickListing} >
                   {item.title} 
                  </a>
                  <div className="kt-quick-search__item-desc">
                    {item.address}
                  </div>
                  <div className="kt-quick-search__item-desc">
                    {item.desc}
                  </div>
                </div>
              </div>
          </React.Fragment>
        ))}
          </div>
          :
          <div>
              <div className="kt-quick-search__category"> Listings <Badge  color="secondary" badgeContent={data.length}>
            <RecentActorsIcon />
               </Badge></div>
               <Spinner animation="grow" />
          </div>
          }
      </div>
    )
  }

export default SearchDropdownListing
