import React from "react";
import { Link } from "react-router-dom";
import Dropdown from "react-bootstrap/Dropdown";
import { connect, useDispatch } from "react-redux";
import { toAbsoluteUrl } from "../../../_metronic";
import HeaderDropdownToggle from "../content/CustomDropdowns/HeaderDropdownToggle";
import * as tabRedux from '../../modules/_tab/_redux/tabRedux'

class UserProfile extends React.PureComponent {
  render() {
    const { user, showAvatar } = this.props;
    const handleOnClick = (e) => {
      // e.preventDefault();
      console.log(e)
      // this.props.callTabManager()
      this.props.callTabManager({ name: e });
      // console.log(e.target.attributes.getNamedItem("profileId").value);
      //  dispatch(modalRedux.actions.openModal('ContactProfileModal',{ contactProfileId: e.target.attributes.getNamedItem("contactProfileId").value }))
      // dispatch(tabRedux.actions.callTabManager({name: 'SingleListing',id: e.target.attributes.getNamedItem("listing-id").value, navTitle: 'Listing '+ e.target.attributes.getNamedItem("listing-title").value}));
      console.log('The link was clicked.');
    }

    return (
      <Dropdown className=" kt-header__topbar-item kt-header__topbar-item--user" drop="down" alignRight>
        <Dropdown.Toggle
          as={HeaderDropdownToggle}
          id="dropdown-toggle-user-profile"
        >
          {/* If profile picture does not exists show default image */}
          <img alt="Pic" src={user?.profile_picture ? user.profile_picture : toAbsoluteUrl("/media/users/default.jpg")} />


          <span className="kt-header__topbar-username kt-hidden-mobile">
            {user?.contact?.first_name} {user?.contact?.last_name}
          </span>

        </Dropdown.Toggle>
        <Dropdown.Menu className="dropdown-menu dropdown-menu-fit dropdown-menu-right dropdown-menu-anim dropdown-menu-top-unround dropdown-menu-xl">
          {/** ClassName should be 'dropdown-menu dropdown-menu-fit dropdown-menu-right dropdown-menu-anim dropdown-menu-top-unround dropdown-menu-xl' */}
          <div
            className="kt-user-card kt-user-card--skin-dark kt-notification-item-padding-x darkBlue-background"
          >
            <div className="kt-user-card__avatar">
              {/* If profile picture does not exists show default image */}
              <img alt="Pic" src={user?.profile_picture ? user.profile_picture : toAbsoluteUrl("/media/users/default.jpg")} />

              <span className="kt-badge kt-hidden kt-badge--lg kt-badge--rounded kt-badge--bold kt-font-success">
                S
              </span>

            </div>
            <div className="kt-user-card__personalInfo">

              <span className='full_name'> {user?.contact?.first_name} {user?.contact?.last_name} </span><br />
              <span className='organization'> {user?.organization ? user.organization : 'Wiseberry Penrith'} </span><br />
              <span className='position'> {user?.position ? user.position : 'Senior Marketing Officer'} </span><br />
            </div>
            {/* <div className="kt-user-card__badge">
              <span className="btn btn-success btn-sm btn-bold btn-font-md">
                23 messages
              </span>
            </div> */}
          </div>
          <div className="kt-notification">
            <a onClick={()=> handleOnClick('MyProfile')} className="kt-notification__item">
              <div className="kt-notification__item-icon">
                <i className="flaticon2-calendar-3 kt-font-success" />
              </div>
              <div className="kt-notification__item-details">
                <div className="kt-notification__item-title kt-font-bold">
                  My Profile
                </div>
                <div className="kt-notification__item-time">
                  Manage my details
                </div>
              </div>
            </a>

            <a className="kt-notification__item">
              <div className="kt-notification__item-icon">
              <i className="fas fa-bell"></i>
              </div>
              <div className="kt-notification__item-details">
                <div className="kt-notification__item-title kt-font-bold">
                  Notifications
                </div>
                <div className="kt-notification__item-time">
                  Manage my notification preference
                </div>
              </div>
            </a>
            <div className="kt-notification__custom justify-content-center">
              <Link
                to="/logout"
                className="btn darkGrey-button btn-sm btn-bold w-50 text-white"
              >
                Sign Out
              </Link>
            </div>
          </div>
        </Dropdown.Menu>
      </Dropdown>
    );
  }
}

const mapStateToProps = ({ auth: { user } }) => ({
  user
});

const actions = {
  callTabManager: tabRedux.actions.callTabManager
};

export default connect(mapStateToProps,actions)(UserProfile);
