import { persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage";
import { put, takeLatest, delay, select} from "redux-saga/effects";
import { getAllTenants, getTenantById, getTenantMatchedData, getTenantTimeline } from "../crud/tenantsCrud";

export const actionTypes = {
    TenantsLoading: "[TenantsLoading]",
    TenantFetched: "[TenantFetched]",
    TenantFetchAsync: "[TenantFetchAsync]",
    TenantFetchTabs: "[TenantFetchTabs]",
    TenantsFetched: "[TenantsFetched]",
    TenantCreated: "[TenantCreated]",
    TenantUpdated: "[TenantUpdated]",
    TenantDeleted: "[TenantDeleted]",
    TenantsDeleted: "[TenantsDeleted]",
    TenantsStatusUpdated: "[TenantsStatusUpdated]",
    TenantFetchNotes: "[TenantFetchNotes]",
    TenantFetchMatched: "[TenantFetchMatched]",
    TenantFetchContacts: "[TenantFetchContacts]",
    TenantFetchDetails: "[TenantFetchDetails]",
    TenantFetchTimeline: "[TenantFetchTimeline]",
    TenantFetchMail: "[TenantFetchMail]",
    TenantToggleCont: "[TenantToggleCont]",
    TenantSelectedId: "[TenantSelectedId]",
    TenantSelectedChangeId: "[TenantSelectedChangeId]",
    TenantSelected: "[TenantSelected]",
    
};

const initialTenantsState = {
    listLoading: true,
    actionsLoading: false,
    totalCount: 0,
    entities: null,
    tenantForEdit: undefined,
    error: null,
    matched:null,
    notes:null,
    contacts: null,
    details:null,
    timeline:null,
    mail:null,
    viewTenant:{
        id: null,
        index: null,
        isLast: false,
    },
    continuousMood: false,
};

export const reducer = persistReducer(
    { storage, key: "wol-auth", whitelist: ["tenants"] },
    (state = initialTenantsState, action) => {
        switch (action.type) {

            case actionTypes.TenantsFetched: {
                const payload = action.payload;
                console.log(payload)
                return {
                    ...state,
                    // totalCount:payload.meta.total,
                    entities: payload,
                    listLoading: false
                }
            }
            case actionTypes.TenantFetched: {
                const {data} = action.payload;
                return {
                    ...state,
                    profile: data
                }
            }

            case actionTypes.TenantToggleCont: {
                const {data} = action.payload;
                return {
                    ...state,
                    continuousMood: action.payload
                }
            }

            case actionTypes.TenantSelectedId: {
                return {
                    ...state,
                    viewTenant : {
                        id: action.payload.id,
                        index: action.payload.index
                    }
                }
            }
            case actionTypes.TenantError: {
                return {
                    ...state,
                    loading: false,
                    error: action.error
                };
            }
            case actionTypes.TenantFetchMatched: {
                const payload = action.payload;
                console.log(payload.data)
                return {
                    ...state,
                    matched: payload.data,
                    error: action.error
                };
            }
            case actionTypes.TenantFetchNotes: {
                return {
                    ...state,
                    loading: false,
                    error: action.error
                };
            }
            case actionTypes.TenantFetchContacts: {
                return {
                    ...state,
                    loading: false,
                    error: action.error
                };
            }
            case actionTypes.TenantFetchDetails: {
                return {
                    ...state,
                    loading: false,
                    error: action.error
                };
            }
            case actionTypes.TenantFetchTimeline: {
                const payload = action.payload;
                console.log(payload.data)
                return {
                    ...state,
                    timeline: payload.data,
                    error: action.error
                };
            }
            case actionTypes.TenantFetchMail: {
                return {
                    ...state,
                    loading: false,
                    error: action.error
                };
            }

            default:
                return state;
        }
    }
);

export const actions = {

    tenantsLoading: () => ({ type: actionTypes.TenantsLoading}),
    tenantToggleCont: (data) => ({ type: actionTypes.TenantToggleCont, payload:  data }),

    tenantSelectedId: data => ({ type: actionTypes.TenantSelectedId, payload:  data }),
    tenantSelectedChangeId: data => ({ type: actionTypes.TenantSelectedChangeId, payload:  data }),
    tenantSelected: data => ({ type: actionTypes.TenantSelected, payload:  data }),

    tenantFetchAsync: data => ({ type: actionTypes.TenantFetchAsync, payload:  data }),
    tenantFetchTabs: data => ({ type: actionTypes.TenantFetchTabs, payload:  data }),
    tenantFetched: data => ({ type: actionTypes.TenantFetched, payload: { data } }),
    tenantsFetched: (data) => ({ type: actionTypes.TenantsFetched, payload: data }),
    tenantCreated: data => ({ type: actionTypes.TenantCreated, payload: { data } }),
    tenantUpdated: data => ({ type: actionTypes.TenantUpdated, payload: { data } }),
    tenantDeleted: data => ({ type: actionTypes.TenantDeleted, payload: { data } }),
    tenantsDeleted: data => ({ type: actionTypes.TenantsDeleted, payload: { data } }),
    tenantsStatusUpdated: data => ({ type: actionTypes.TenantsStatusUpdated, payload: { data } }),
    tenantError: error => ({ type: actionTypes.TenantError, payload: { error } }),

    tenantFetchNotes: data => ({ type: actionTypes.TenantFetchNotes, payload: { data } }),
    tenantFetchMatched: data => ({ type: actionTypes.TenantFetchMatched, payload: { data } }),
    tenantFetchContacts: data => ({ type: actionTypes.TenantFetchContacts, payload: { data } }),
    tenantFetchDetails: data => ({ type: actionTypes.TenantFetchDetails, payload: { data } }),
    tenantFetchTimeline: data => ({ type: actionTypes.TenantFetchTimeline, payload: { data } }),
    tenantFetchMail: data => ({ type: actionTypes.TenantFetchMail, payload: { data } }),
};

export const getTenants = (state) => state.tenants;


export function* sagaTenants() {

    yield takeLatest(actionTypes.TenantsLoading, function* tenantsLoading() {
        console.log('called TenantsLoading');
        const data = yield getAllTenants();
        console.log(data.data)
        // yield delay(2000);
        console.log(data);
        yield put(actions.tenantsFetched(data.data));
        
    });

    yield takeLatest(actionTypes.TenantSelected, function* tenantSelecteds({payload}) {
        console.log('called TenantSelected');
        let project = yield select(getTenants);
        let index = project.entities.findIndex(item => item.id === payload);
        // yield delay(2000);
         console.log(project.entities);
         console.log(payload);
        console.log(index);
        yield put(actions.tenantSelectedId({id:payload, index: index  }));
        // yield put(actions.tenantsFetched(data.data));
        
    });

    yield takeLatest(actionTypes.TenantSelectedChangeId, function* tenantSelected({payload}) {
        console.log(payload);
        let tenants = yield select(getTenants);
        console.log(tenants.viewTenant.index)

        let data  = tenants.entities[tenants.viewTenant.index];
        let index = tenants.entities.findIndex(item => item.id === payload);
        // yield delay(2000);
         console.log(tenants.entities);
         console.log(payload);
        console.log(data);

        let newIndex = tenants.viewTenant.index +1;
        let newId = tenants.entities[newIndex].id
        console.log(newId)
        console.log(newIndex)
        yield put(actions.tenantSelectedId({id:newId, index: newIndex  }));
        // yield put(actions.tenantsFetched(data.data));
        
    });
    


    yield takeLatest(actionTypes.TenantFetchAsync, function* tenantFetchAsync({payload}) {
        console.log('called socialFeedPostAsync');
        console.log(payload);
        const tenant = yield getTenantById(payload);
        yield delay(2000);
        yield put(actions.tenantFetched(tenant));


    });

    yield takeLatest(actionTypes.TenantFetchTabs, function* tenantFetchTabs({payload}) {
        console.log(payload);
        let result = {};
    switch (payload.tab) {
        case "matched":
            result = yield getTenantMatchedData(payload);
             console.log(result.data.data)
              yield put(actions.tenantFetchMatched(result.data.data));
            break;
        case "notes":
           
        break;
        case "contacts":
            
        break;
        case "details":
            
        break;
        case "timeline":
            result = yield getTenantTimeline(payload);
        console.log(result.data)

        yield put(actions.tenantFetchTimeline(result.data));
       
        break;
        case "mail":
            
        break;
        default:
            console.log('something happened')
        }


    });
}
