// Method from server should return QueryResultsModel(items: any[], totalsCount: number)
// items => filtered/sorted result
export function findTenants(queryParams) {
  return axios.post(`${CONTACTS_URL}/find`, { queryParams });
}

// UPDATE => PUT: update the tenant on the server
export function updateTenant(tenant) {
  return axios.put(`${CONTACTS_URL}/${tenant.id}`, { tenant });
}

// UPDATE Status
export function updateStatusForTenants(ids, status) {
  return axios.post(`${CONTACTS_URL}/updateStatusForTenants`, {
    ids,
    status
  });
}

// DELETE => delete the tenant from the server
export function deleteTenant(tenantId) {
  return axios.delete(`${CONTACTS_URL}/${tenantId}`);
}

// DELETE Tenants by ids
export function deleteTenants(ids) {
  return axios.post(`${CONTACTS_URL}/deleteTenants`, { ids });
}

