import React, { Fragment } from "react";
import {reduxForm, FieldArray } from "redux-form";
import validate from "./validate";
import ThemeHeader from "../../../../partials/content/ThemeHeader";
import ThemeButton from "../../../../partials/button/ThemeButton";
import DefaultModal from "../../../../partials/modal/DefaultModal";
import ContactForm from "../../../contacts/modals/contactFormModal/ContactForm";
import FormContacts from "../../../../partials/form/partials/FormContacts";

const AddNewListingContacts = (props) => {
  const { handleSubmit, previousPage } = props;

  const [modalShow, setModalShow] = React.useState(false);

  return (
    <Fragment>
      {modalShow ? (
        <DefaultModal show={modalShow} onHide={() => setModalShow(false)}>
          <div>
            <ContactForm closeModal={() => setModalShow(false)} onSubmit={(e) => console.log(e)} />
          </div>

        </DefaultModal>
      ) : (
        ""
      )}
      <ThemeHeader
        right={
          <Fragment>
            <ThemeButton className="mr-2 lightGrey-button" >Remove </ThemeButton>
            <ThemeButton className="theme-button green-button" onClick={()=> setModalShow(true) }>Add</ThemeButton>
          </Fragment>
        }
      />
      <form onSubmit={handleSubmit}>
      <FieldArray name="contacts" component={FormContacts}/>
      </form>
    </Fragment>
  );
};

export default reduxForm({
  form: "AddNewListingsForm", //                 <------ same form name
  destroyOnUnmount: false, //        <------ preserve form data
  forceUnregisterOnUnmount: true, // <------ unregister fields on unmount
  validate,
})(AddNewListingContacts);
