import React, { Fragment } from "react";
import Toggle from "react-toggle";
import "react-toggle/style.css";
import "./toggleSwitch.scss";

const ToggleSwitch = ({ input, label, name, defaultChecked, labelLeft ,labelTrue , labelFalse}) => {
  return (
    <Fragment>
      <div className="d-flex ">
        {label && labelLeft ? <label className="m-auto p-1">{label}</label> : ""}
        <div className="toggle-switch">
          <Toggle
            name={name}
            checked={input?.value ? true : false}
            icons={{
              checked: labelTrue,
              unchecked: labelFalse,
            }}
            onChange={input?.onChange}
          />
        </div>
        {label && !labelLeft ? <label className='p-1'>{label}</label> : ""} 
      </div>
    </Fragment>
  );
};

ToggleSwitch.defaultProps = {
  label: false,
  labelLeft: false,
  labelTrue: "Yes",
  labelFalse: "No"
 };

export default ToggleSwitch;
