import React from 'react'
import { connect } from 'react-redux'
import { submit } from 'redux-form'
import { Button } from '@material-ui/core'


const RemoteFormSubButton = ({ dispatch , formName, name}) => (
  <Button
  className="green-button text-white"
  variant="contained"
  onClick={() => dispatch(submit(formName))}
>
  {name}
</Button>
)
//                                  ^^^^^^^^^^^^ name of the form

export default connect()(RemoteFormSubButton)