import React, { Fragment, useEffect, useState } from "react";
import { Table, Form, Col } from "react-bootstrap";
import { Field } from "redux-form";
import TextInput from "../TextInput";
import TextArea from "../TextArea";
import { IconButton } from "@material-ui/core";
import ThemeButton from "../../button/ThemeButton";
import ThemeHeader from "../../content/ThemeHeader";
import cuid from "cuid";
import SortableTable from "../sortableTable/SortableTable";
import Handle from "../../component/handle/Handle";
import RadioInput from "../RadioInput";
import MultiSelect from "../MultiSelect";

const FormContacts = (props) => {
  const { fields , update } = props;
  const [featuresValue, setFeaturesValue] = useState("");

  useEffect(() => {
    fields.push({
      id: false,
      isSelected: false,
      role: "bedrooms",
      name: "",
      phone: "4567890-",
      email: "asdsa@asda.com",
    });
    fields.push({
      id: false,
      isSelected: false,
      role: ['sellerSolicitor'],
    });
    fields.push({ id: false, isSelected: false, role: "seller" });
    fields.push({
      id: false,
      isSelected: false,
      role: {
        label: "Sarah",
        value: "3",
      },
    });
  }, []);

  useEffect(() => {
    fields.removeAll();
    fields.push({
      id: false,
      isSelected: false,
      role: "bedrooms",
      name: "",
      phone: "asdasdasdasd-",
      email: "asdsa@asda.com",
    });
    fields.push({
      id: false,
      isSelected: false,
      role: ['sellerSolicitor'],
    });
    fields.push({ id: false, isSelected: false, role: "seller" });
    fields.push({
      id: false,
      isSelected: false,
      role: [3],
    });
  }, [update]);

  useEffect(() => {
    handleRemove();
  }, [props.deleteItem]);

  const handleAddFeature = () => {
    let obj = {};
    obj.features = document.getElementById("features").value;
    obj.comments = document.getElementById("featureComments").value;
    obj.preference = document.getElementById("featurePreference")?.value
      ? document.getElementById("featurePreference").value
      : "";
    obj.landSize = document.getElementById("featureLandsize")?.value
      ? document.getElementById("featureLandsize").value
      : "";
    obj.landType = document.getElementById("featuresLandType")?.value
      ? document.getElementById("featuresLandType").value
      : "";
    fields.push(obj);
  };

  const handleRemove = () => {
    for (let i = fields.length - 1; i >= 0; i--) {
      if (fields.get(i).isSelected) {
        fields.remove(i);
      }
    }

    document.getElementById("checkToggle").checked = false;
  };

  const handleSelectAllToggle = (e) => {
    const all = fields.getAll();
    const selected = document.getElementById("checkToggle").checked
      ? true
      : false;

    fields.removeAll();
    all.forEach((item, index) => {
      all[index].isSelected = selected;
      fields.push(item);
    });

  };

  const myBuyerOptions = [
    {
      label: "Seller",
      value: "seller",
    },
    {
      label: "Seller Solicitor",
      value: "sellerSolicitor",
    },
    {
      label: "Sarah",
      value: "3",
    },
  ];
  return (
    <Fragment>
      <div className="tableTheme theme-table-striped">
        <Table striped bordered hover size="sm">
          <thead>
            <tr>
              <th>
                {" "}
                <input
                  id="checkToggle"
                  type="checkbox"
                  onChange={handleSelectAllToggle}
                />
              </th>
              <th>Role</th>
              <th>Name</th>
              <th>Phone</th>
              <th>Email</th>
            </tr>
          </thead>
          <tbody></tbody>
          <SortableTable fields={fields}>
            {fields.map((item, index) => (
              <Fragment key={index}>
                <td>
                  <Field
                    name={`${item}.isSelected`}
                    component={RadioInput}
                    type="checkbox"
                  />
                </td>
                <td>
                  <Field
                    name={`${item}.role`}
                    placeholder="Select type"
                    component={MultiSelect}
                    showBottom={false}
                    isMulti={false}
                    options={myBuyerOptions}
                  />
                </td>
                <td>
                  {fields?.get(index)?.features !== "landsize" ? (
                    <Field
                      disabled
                      name={`${item}.preference`}
                      component={TextInput}
                      type="text"
                      placeholder="preference"
                    />
                  ) : (
                    <Fragment>
                      <Form.Group className="d-flex">
                        <Col sm={6} className="pr-3 pr-md-0 pb-3 pb-md-3">
                          <Field
                            name={`${item}.landSize`}
                            type="text"
                            component={TextInput}
                            placeholder=""
                          />
                        </Col>
                        <Col sm={6} className="pr-3 pr-md-0 pb-3 pb-md-3">
                          <Field
                            className="form-control"
                            name={`${item}.landType`}
                            component="select"
                          >
                            <option value="m2">m2</option>
                            <option value="acer">acer</option>
                          </Field>
                        </Col>
                      </Form.Group>
                    </Fragment>
                  )}
                </td>
                <td>
                  <Field
                    disabled
                    name={`${item}.phone`}
                    component={TextInput}
                    type="text"
                    edit
                    placeholder="preference"
                  />
                </td>

                <td className="d-flex">
                  <Field
                    disabled
                    name={`${item}.comments`}
                    component={TextInput}
                    type="text"
                    placeholder="Enter comment here"
                  />
                  <Handle />
                </td>
              </Fragment>
            ))}{" "}
          </SortableTable>
        </Table>
      </div>
    </Fragment>
  );
};

export default FormContacts;
