import { persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage";
import { put, takeLatest, delay} from "redux-saga/effects";
import { getAllBuyers, getBuyerById, getBuyerMatchedData, getBuyerTimeline } from "../crud/buyersCrud";

export const actionTypes = {
    BuyersLoading: "[BuyersLoading]",
    BuyerFetched: "[BuyerFetched]",
    BuyerFetchAsync: "[BuyerFetchAsync]",
    BuyerFetchTabs: "[BuyerFetchTabs]",
    BuyersFetched: "[BuyersFetched]",
    BuyerCreated: "[BuyerCreated]",
    BuyerUpdated: "[BuyerUpdated]",
    BuyerDeleted: "[BuyerDeleted]",
    BuyersDeleted: "[BuyersDeleted]",
    BuyersStatusUpdated: "[BuyersStatusUpdated]",
    BuyerFetchNotes: "[BuyerFetchNotes]",
    BuyerFetchMatched: "[BuyerFetchMatched]",
    BuyerFetchContacts: "[BuyerFetchContacts]",
    BuyerFetchDetails: "[BuyerFetchDetails]",
    BuyerFetchTimeline: "[BuyerFetchTimeline]",
    BuyerFetchMail: "[BuyerFetchMail]",
};

const initialBuyersState = {
    listLoading: true,
    actionsLoading: false,
    totalCount: 0,
    entities: null,
    buyerForEdit: undefined,
    error: null,
    matched:null,
    notes:null,
    contacts: null,
    details:null,
    timeline:null,
    mail:null,
};

export const reducer = persistReducer(
    { storage, key: "wol-auth", whitelist: ["buyers"] },
    (state = initialBuyersState, action) => {
        switch (action.type) {

            case actionTypes.BuyersFetched: {
                const payload = action.payload;
                console.log(payload.data)
                return {
                    ...state,
                    totalCount:payload.meta.total,
                    entities: payload.data,
                    listLoading: false
                }
            }
            case actionTypes.BuyerFetched: {
                const {data} = action.payload;
                return {
                    ...state,
                    profile: data
                }
            }
            case actionTypes.BuyerError: {
                return {
                    ...state,
                    loading: false,
                    error: action.error
                };
            }
            case actionTypes.BuyerFetchMatched: {
                const payload = action.payload;
                console.log(payload.data)
                return {
                    ...state,
                    matched: payload.data,
                    error: action.error
                };
            }
            case actionTypes.BuyerFetchNotes: {
                return {
                    ...state,
                    loading: false,
                    error: action.error
                };
            }
            case actionTypes.BuyerFetchContacts: {
                return {
                    ...state,
                    loading: false,
                    error: action.error
                };
            }
            case actionTypes.BuyerFetchDetails: {
                return {
                    ...state,
                    loading: false,
                    error: action.error
                };
            }
            case actionTypes.BuyerFetchTimeline: {
                const payload = action.payload;
                console.log(payload.data)
                return {
                    ...state,
                    timeline: payload.data,
                    error: action.error
                };
            }
            case actionTypes.BuyerFetchMail: {
                return {
                    ...state,
                    loading: false,
                    error: action.error
                };
            }

            default:
                return state;
        }
    }
);

export const actions = {
    buyersLoading: () => ({ type: actionTypes.BuyersLoading}),
    buyerFetchAsync: data => ({ type: actionTypes.BuyerFetchAsync, payload:  data }),
    buyerFetchTabs: data => ({ type: actionTypes.BuyerFetchTabs, payload:  data }),
    buyerFetched: data => ({ type: actionTypes.BuyerFetched, payload: { data } }),
    buyersFetched: (data) => ({ type: actionTypes.BuyersFetched, payload: data }),
    buyerCreated: data => ({ type: actionTypes.BuyerCreated, payload: { data } }),
    buyerUpdated: data => ({ type: actionTypes.BuyerUpdated, payload: { data } }),
    buyerDeleted: data => ({ type: actionTypes.BuyerDeleted, payload: { data } }),
    buyersDeleted: data => ({ type: actionTypes.BuyersDeleted, payload: { data } }),
    buyersStatusUpdated: data => ({ type: actionTypes.BuyersStatusUpdated, payload: { data } }),
    buyerError: error => ({ type: actionTypes.BuyerError, payload: { error } }),

    buyerFetchNotes: data => ({ type: actionTypes.BuyerFetchNotes, payload: { data } }),
    buyerFetchMatched: data => ({ type: actionTypes.BuyerFetchMatched, payload: { data } }),
    buyerFetchContacts: data => ({ type: actionTypes.BuyerFetchContacts, payload: { data } }),
    buyerFetchDetails: data => ({ type: actionTypes.BuyerFetchDetails, payload: { data } }),
    buyerFetchTimeline: data => ({ type: actionTypes.BuyerFetchTimeline, payload: { data } }),
    buyerFetchMail: data => ({ type: actionTypes.BuyerFetchMail, payload: { data } }),
};


export function* sagaBuyers() {

    yield takeLatest(actionTypes.BuyersLoading, function* buyersLoading() {
        console.log('called BuyersLoading');
        const data = yield getAllBuyers();
        console.log(data.data.data)
        // yield delay(2000);
        console.log(data);
        yield put(actions.buyersFetched(data.data));
        
    });


    yield takeLatest(actionTypes.BuyerFetchAsync, function* buyerFetchAsync({payload}) {
        console.log('called socialFeedPostAsync');
        console.log(payload);
        const buyer = yield getBuyerById(payload);
        yield delay(2000);
        yield put(actions.buyerFetched(buyer));


    });

    yield takeLatest(actionTypes.BuyerFetchTabs, function* buyerFetchTabs({payload}) {
        console.log(payload);
        let result = {};
    switch (payload.tab) {
        case "matched":
            result = yield getBuyerMatchedData(payload);
             console.log(result.data.data)
              yield put(actions.buyerFetchMatched(result.data.data));
            break;
        case "notes":
           
        break;
        case "contacts":
            
        break;
        case "details":
            
        break;
        case "timeline":
            result = yield getBuyerTimeline(payload);
        console.log(result.data)

        yield put(actions.buyerFetchTimeline(result.data));
       
        break;
        case "mail":
            
        break;
        default:
            console.log('something happened')
        }


    });
}
