import { arrayMove } from "react-sortable-hoc";
import React, { Children, PureComponent } from "react";

import SortableReduxForm from "./SortableTableContainer";

class SortableTable extends PureComponent {
  state = {
    items: Children.toArray(this.props.children)
  };

  onSortEnd = ({ oldIndex, newIndex }) => {
    console.log("onSortEnd", oldIndex, newIndex);
    this.props.fields.move(oldIndex, newIndex);
    this.setState({
      items: arrayMove(this.state.items, oldIndex, newIndex)
    });
  };

  render() {
    console.log('sl', this.state.items)
    console.log('sl', this.props)
    console.log('sl', Children.toArray(this.props.children))
    return (
      <SortableReduxForm
        initialValues={this.props.initialValues}
        onSubmit={this.handleSubmit}
        items={Children.toArray(this.props.children)}
        onSortEnd={this.onSortEnd}
        lockAxis="y"
        lockToContainerEdges
        lockOffset={["0%", "100%"]}
        useDragHandle
      />
    );
  }
}

export default SortableTable;
