import React, { Fragment, useState, useEffect } from "react";
import { Row, ButtonGroup, ToggleButton } from "react-bootstrap";
import cuid from "cuid";
import ThemeButton from "../../../../../partials/button/ThemeButton";
import NoteLists from "../../../../../partials/component/notes/NoteLists";
import NoteForm from "../../../../../partials/form/partials/note/NoteForm";

const TenantNote = (props) => {
  const { fields } = props;
  const [noteType, setNoteType] = useState("All Notes");
  const addNote = (e) => {
    fields.push(e);
    console.log(e);
  };
  const noteTypeOptions = [
    { name: "All Notes", value: "All Notes" },
    { name: "This Listing Only", value: "This Listing Only" },
  ];

  const handleNoteType = (e) => {
    setNoteType(e.currentTarget.value);
  };

  useEffect(() => {
    console.log(props);
    // setDiaryType(props.diaryType)
    // setDiaryOf(props.diaryOf)
    // setDiaryView(props.view)
  }, []);

  return (
    <Fragment>
      <NoteForm addNote={addNote} />
      <div className="d-flex justify-content-end">
        <div className="pb-2">
          <ThemeButton icon>
            <i class="fas fa-download"></i>
          </ThemeButton>
          <ButtonGroup className="themeButton-toggle-group pl-2" toggle>
            {noteTypeOptions.map((radio, idx) => (
              <ToggleButton
                key={cuid()}
                type="radio"
                name="radio"
                value={radio.value}
                checked={noteType === radio.value}
                onChange={handleNoteType}
              >
                {radio.name}
              </ToggleButton>
            ))}
          </ButtonGroup>
        </div>
      </div>
      <Row></Row>
      <NoteLists />
    </Fragment>
  );
};

export default TenantNote;
