import React from "react";

const ThemeButton = ({onClick,children, className, icon, ...others}) => {
  const styleProps = className ? className : (icon ? "lightGrey-icon-button" : "lightGrey-button") ;
  const classes = icon ? " icon-btn" : " btn themeButton ";
  return (<button onClick={onClick} type="button" {...others} className={ styleProps + classes}>{children}</button>
  )
}


ThemeButton.defaultProps = {
  // classNames: "lightGrey-button",
 };

export default ThemeButton
