import React, { useEffect } from "react";
import { Checkbox } from "@material-ui/core";
import {
  useTable,
  useSortBy,
  useGlobalFilter,
  useRowSelect,
} from "react-table";
import { Waypoint } from "react-waypoint";

const IndeterminateCheckbox = React.forwardRef(
  ({ indeterminate , ...rest }, ref) => {
    const defaultRef = React.useRef();
    console.log()
    const resolvedRef = ref || defaultRef;
    React.useEffect(() => {
      resolvedRef.current.indeterminate = indeterminate;
    }, [resolvedRef, indeterminate]);

    return (
      <>
        {/* e.stopPropagation() will stop it from row onClick */}
        
 {/* {rest.allSelected ?  <Checkbox   onClick={(e) => rest.allSelected(e)} className="table-checkbox" ref={resolvedRef} {...rest} /> : rest.is_selectable ?  <Checkbox   onClick={(e) => {e.stopPropagation()}} className="table-checkbox" ref={resolvedRef} {...rest} /> : <Checkbox   className="table-checkbox display-none" ref={resolvedRef} />} */}
        {rest.is_selectable ?  <Checkbox   onClick={(e) => {e.stopPropagation()}} className="table-checkbox" ref={resolvedRef} {...rest} /> : <Checkbox   className="table-checkbox display-none" ref={resolvedRef} />}
        
      </>
    );
  }
);

const DynamicTableEdit = ({ columns, data, update, handleChange, isEditable,showFooter ,allSelectable,handleRowClick, allSelected }) => {
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    preGlobalFilteredRows,
    prepareRow,
    selectedFlatRows,
    footerGroups,
    state: { sortBy },
  } = useTable(
    {
      columns,
      data,
    },

    isEditable
      ? (hooks) => {
          hooks.allColumns.push((columns) => [
            // Let's make a column for selection
            {
              id: "selection",
              // The header can use the table's getToggleAllRowsSelectedProps method
              // to render a checkbox.  Pagination is a problem since this will select all
              // rows even though not all rows are on the current page.  The solution should
              // be server side pagination.  For one, the clients should not download all
              // rows in most cases.  The client should only download data for the current page.
              // In that case, getToggleAllRowsSelectedProps works fine.
              Header: ({ getToggleAllRowsSelectedProps }) => (
                <div>
                  <IndeterminateCheckbox allSelected={allSelected}  {...getToggleAllRowsSelectedProps()} />
                </div>
              ),
              // The cell can use the individual row's getToggleRowSelectedProps method
              // to the render a checkbox
              Cell: ({ row }) => (
                <div>
                  <IndeterminateCheckbox is_selectable={allSelectable ? allSelectable : row.original.is_selectable} {...row.getToggleRowSelectedProps()} />
                </div>
              ),
            },
            ...columns,
          ]);
        }
      : "",
    useGlobalFilter, // useGlobalFilter!
    useSortBy,
    useRowSelect
  );

  React.useEffect(() => {
    console.log("sort");
  }, [sortBy]);

  console.log(rows);
  console.log(preGlobalFilteredRows);

  useEffect(() => {
    console.log(selectedFlatRows.map((d) => d.original));
    if (isEditable) {
      handleChange(selectedFlatRows.map((d) => d.original));
    }
  }, [selectedFlatRows]);

  const handleRowClicked = ({ original }) => {
    console.log(original);
    if(handleRowClick){
      handleRowClick(original)
    }
  };

  return (
    <div className="tableFixHead theme-table-striped">
      <table {...getTableProps()} className="table">
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps()}>{column.render("Header")}</th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {rows.map((row, i) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}  onClick={() => handleRowClicked(row)}>
                {row.cells.map((cell, i) => {
                  return (
                    <td key={i} {...cell.getCellProps()}>
                      {cell.render("Cell")}
                    </td>
                  );
                })}
              </tr>
            );
          })}
                      <tr>
              <td>
                <Waypoint onEnter={update} />
              </td>
            </tr>
        </tbody>
        {/* {showFooter ? (
          <tfoot>
            {footerGroups.map((group) => (
              <tr {...group.getFooterGroupProps()}>
                {group.headers.map((column) => (
                  <td {...column.getFooterProps()}>
                    {column.render("Footer")}
                  </td>
                ))}
              </tr>
            ))}
          </tfoot>
        ) : (
          ""
        )} */}
      </table>
    </div>
  );
};
DynamicTableEdit.defaultProps = {
  allSelectable: true,
 };
export default DynamicTableEdit;
